import React, {Component} from "react";

interface IProductInCartProps {
	product: {
		id: string;
		productImage: string;
		productPrice: number;
		productQuantity: number;
		productDescription: {
			productName: string;
		};
	};
	onRemoveClick: (id: string) => void;
	onExitSave: (id: string, quantity: number) => void;
}
class ProductInCart extends Component {
	props: IProductInCartProps;

	constructor(props: IProductInCartProps) {
		super(props);
		this.props = props;
	}

	componentDidMount = () => {
		$("#modalCart").on("closed.zf.reveal", () => {
			const quantityInput = document.querySelector(
				"#quantityInput"
			) as HTMLInputElement;
			quantityInput &&
				this.props.onExitSave(
					this.props.product.id,
					parseInt(quantityInput.value)
				);
		});
	};

	render() {
		return (
			<div className="cell grid-x padding-vertical-1">
				<div className="cell grid-x padding-vertical-1">
					<div className="cell medium-4 text-center">
						<img src={this.props.product.productImage} alt="Item image" />
					</div>
					<small className="cell medium-6 padding-1">
						{this.props.product.productDescription.productName}
					</small>
					<div className="cell medium-2 grid-x align-right">
						<input
							id="quantityInput"
							type="number"
							name="quantity"
							pattern="number"
							defaultValue={this.props.product.productQuantity.toLocaleString()}
							required
							aria-errormessage="quantityError"
							style={{width: "60px"}}
						/>
						<span className="form-error" id="quantityError">
							Please provide a correct quantity number.
						</span>
						<div>
							<a
								href="#"
								className="button hollow alert"
								onClick={(e) => {
									this.props.onRemoveClick(this.props.product.id);
								}}>
								Remove
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default ProductInCart;
