import React, {Component} from "react";
import {QueryRenderer, graphql} from "react-relay";
import {withRouter, RouteComponentProps} from "react-router-dom";
import environment from "../../../../schema/environment";
import CreateOrderProductMutation from "./CreateOrderProductMutation";

interface IOrderProductCreateState {
	numberOfProductSelect: number;
}
interface IOrderProductObj {
	orderId: string;
	customerName: string;
	productId: string;
	productName: string;
}
class OrderProductCreate extends Component {
	props: RouteComponentProps;
	state: IOrderProductCreateState;

	constructor(props: RouteComponentProps) {
		super(props);
		this.props = props;
		this.state = {
			numberOfProductSelect: 1
		};
	}

	createOrderProductHandler = (viewerId: string) => {
		const orderProductObj = {} as IOrderProductObj;
		const customerSelect = document.querySelector(
			"#customerSelect"
		) as HTMLSelectElement;
		const productSelects = document.querySelectorAll(
			'[id^="productSelect"]'
		) as NodeListOf<HTMLSelectElement>;
		if (customerSelect && productSelects) {
			const selectedCustomer =
				customerSelect.options[customerSelect.selectedIndex];

			orderProductObj.orderId = selectedCustomer.value;
			orderProductObj.customerName = selectedCustomer.text;

			productSelects.forEach((productSelect, idx) => {
				const selectedProduct =
					productSelect.options[productSelect.selectedIndex];
				const productId = selectedProduct.value;
				const productName = selectedProduct.text;

				orderProductObj.productId = productId;
				orderProductObj.productName = productName;

				CreateOrderProductMutation(
					orderProductObj,
					viewerId,
					idx === productSelects.length - 1
						? () => {
								this.props.history.push("/create/listorderproduct");
						  }
						: null
				);
			});
		}
	};

	render() {
		const OrderProductCreateViewerQuery = graphql`
			query OrderProductCreateViewerQuery {
				viewer {
					id
					allOrders(first: 10) {
						edges {
							node {
								id
								customerName
							}
						}
					}
					allProducts(first: 10) {
						edges {
							node {
								id
								productDescription {
									productName
								}
							}
						}
					}
				}
			}
		`;

		return (
			<QueryRenderer
				environment={environment}
				query={OrderProductCreateViewerQuery}
				variables={{}}
				render={({error, props}) => {
					if (error) {
						return (
							<p>
								<b>Error: {error}</b>
							</p>
						);
					} else if (!props) {
						return (
							<p>
								<b>Loading...</b>
							</p>
						);
					} else {
						let productSelects: JSX.Element[] = [];
						for (let i = 0; i < this.state.numberOfProductSelect; i++) {
							productSelects.push(
								<div className="cell grid-x" key={i}>
									[Belong to] productName:
									<select id={"productSelect" + i}>
										{props.viewer.allProducts.edges.map(
											(
												{
													node
												}: {
													node: {
														id: string;
														productDescription: {productName: string};
													};
												},
												idx: number
											) => {
												return (
													<option key={idx} value={node.id}>
														{node.productDescription.productName}
													</option>
												);
											}
										)}
									</select>
								</div>
							);
						}

						return (
							<div className="grid-x align-center margin-vertical-1">
								<div
									className="grid-x shadow padding-1"
									style={{maxWidth: 300}}>
									<div className="cell grid-x">
										[Belong to] order-customerName:
										<select id="customerSelect">
											{props.viewer.allOrders.edges.map(
												(
													{node}: {node: {id: string; customerName: string}},
													idx: number
												) => {
													return (
														<option key={idx} value={node.id}>
															{node.customerName}
														</option>
													);
												}
											)}
										</select>
									</div>
									{productSelects}
									<div className="cell grid-x">
										<button
											type="button"
											className="button hollow"
											onClick={(e) => {
												this.setState((prevState: IOrderProductCreateState) => {
													return {
														numberOfProductSelect:
															prevState.numberOfProductSelect + 1
													};
												});
											}}>
											More product
										</button>
									</div>
									<div className="cell text-right">
										<button
											type="button"
											className="button"
											onClick={(e) => {
												this.createOrderProductHandler(props.viewer.id);
											}}>
											Submit
										</button>
									</div>
								</div>
							</div>
						);
					}
				}}
			/>
		);
	}
}

export default withRouter(OrderProductCreate);
