import React, {Component} from "react";
import {Link} from "react-router-dom";
import {createFragmentContainer, graphql} from "react-relay";

interface ICartItems {
	items: {
		shopName: string;
		products: {
			id: string;
			productImage: string;
			productPrice: number;
			productQuantity: number;
			productDescription: {
				productName: string;
			};
		}[];
	}[];
}

interface IAddToCartProps {
	shop: {
		shopImage: string;
		shopName: string;
	};
	Product: {
		id: string;
		productImage: string;
		productPrice: number;
		productQuantity: number;
		productDescription: {
			productName: string;
		};
	};
}
const AddToCart = (props: IAddToCartProps) => {
	const addToCartHandler = () => {
		let cartItems = localStorage.getItem("cartItems");
		let cartItemsObj: ICartItems;
		const shopName = props.shop.shopName;
		const orderProduct = Object.assign({}, props.Product);
		orderProduct.productQuantity = 1;

		if (cartItems) {
			cartItemsObj = JSON.parse(cartItems) as ICartItems;
			const shop = cartItemsObj.items.find((currentItem) => {
				return currentItem.shopName === shopName;
			});

			if (shop) {
				const product = shop.products.find((currentProduct) => {
					return currentProduct.id === orderProduct.id;
				});
				if (product) {
					product.productQuantity = product.productQuantity + 1;
				} else {
					shop.products.push(orderProduct);
				}
			} else {
				cartItemsObj.items.push({
					shopName,
					products: [orderProduct]
				});
			}
		} else {
			cartItemsObj = {
				items: [
					{
						shopName: props.shop.shopName,
						products: [orderProduct]
					}
				]
			};
		}
		cartItems = JSON.stringify(cartItemsObj);
		localStorage.setItem("cartItems", cartItems);
		document.dispatchEvent(
			new CustomEvent("localStorageSaved", {
				detail: cartItemsObj
			})
		);
	};

	return (
		<div id="AddToCart">
			<Link to="/shop/test" className="grid-x align-middle">
				<div className="cell small-3 medium-2">
					<img
						className="thumbnail img-with-shadow"
						src={props.shop.shopImage}
						alt="WoolDryer"
					/>
				</div>
				<div className="cell small-9 medium-10 padding-left-1">
					<small>Made and sold by</small>
					<br />
					<p className="margin-0">
						<b className="p">{props.shop.shopName}</b>
						<i className="fas fa-star" />
						<i className="fas fa-star" />
						<i className="fas fa-star" />
						<i className="fas fa-star" />
						<i className="fas fa-star" />
					</p>
				</div>
			</Link>
			<div
				id="productImages"
				className="orbit padding-vertical-1"
				role="region"
				aria-label="product's images"
				data-orbit>
				<div className="orbit-wrapper">
					<div className="orbit-controls">
						<button className="orbit-previous" aria-label="previous image">
							<i className="fas fa-chevron-left" />
						</button>
						<button className="orbit-next" aria-label="next image">
							<i className="fas fa-chevron-right" />
						</button>
					</div>
					<ul className="orbit-container bordered">
						<li className="orbit-slide is-active">
							<div
								className="orbit-image"
								style={{
									backgroundImage: `url(${props.Product.productImage})`
								}}
							/>
						</li>
					</ul>
					<nav className="orbit-bullets">
						<button
							className="is-active"
							data-slide={0}
							aria-label="first slide"
						/>
						{/* <button data-slide={1} aria-label="second slide" /> */}
					</nav>
				</div>
			</div>
			<div>
				<h1 className="h5" id="testTouch">
					{props.Product.productDescription.productName}
				</h1>
				<div className="grid-x small-up-2">
					<p className="cell">
						<b>USD {props.Product.productPrice}</b>
					</p>
					<p className="cell subheader text-right">
						{props.Product.productQuantity} available
					</p>
				</div>
				<p>
					<small>Local taxes included (where applicable)</small>
				</p>
				<div className="text-center">
					<button
						type="button"
						className="button expanded padding-bottom-1"
						onClick={addToCartHandler}>
						Add to cart
					</button>
				</div>
			</div>
			<div className="grid-x align-middle padding-top-1">
				<div className="cell small-2">
					<svg
						xmlns="http://www.w3.org/2000/svg"
						viewBox="0 0 48 48"
						height={48}
						width={48}
						aria-hidden="true"
						focusable="false">
						<g className="cart-frame">
							<path
								className="cart-fill"
								fill="#FFAC5D"
								d="M19,33h20l5-18H15"
							/>
							<path
								fill="#333333"
								d="M43,14H14.7L13,5.8C12.8,5.3,12.5,5,12,5H5C4.3,5,4,5.4,4,6s0.4,1,1,1h6.2l1.8,8c0,0.1,0,0.3,0.1,0.4   l4,17.8c0.1,0.5,0.5,0.8,1,0.8h20c0.6,0,1-0.4,1-1s-0.4-1-1-1H18.8l-3.6-16H43c0.6,0,1-0.4,1-1S43.6,14,43,14z"
							/>
							<path
								fill="#333333"
								d="M22.5,36c-1.9,0-3.5,1.6-3.5,3.5s1.6,3.5,3.5,3.5s3.5-1.6,3.5-3.5S24.3,36,22.5,36z M22.5,41   c-0.8,0-1.5-0.7-1.5-1.5s0.7-1.5,1.5-1.5s1.5,0.7,1.5,1.5S23.2,41,22.5,41z"
							/>
							<path
								fill="#333333"
								d="M34.5,36c-1.9,0-3.5,1.6-3.5,3.5s1.6,3.5,3.5,3.5s3.5-1.6,3.5-3.5S36.3,36,34.5,36z M34.5,41   c-0.8,0-1.5-0.7-1.5-1.5s0.7-1.5,1.5-1.5s1.5,0.7,1.5,1.5S35.3,41,34.5,41z"
							/>
						</g>
						<line
							className="line-right"
							fill="none"
							stroke="#333333"
							strokeWidth={2}
							strokeLinecap="round"
							strokeLinejoin="round"
							x1={37}
							y1={8}
							x2="33.8"
							y2="11.1"
						/>
						<line
							className="line-center"
							fill="none"
							stroke="#333333"
							strokeWidth={2}
							strokeLinecap="round"
							strokeLinejoin="round"
							x1={29}
							y1={6}
							x2={29}
							y2={10}
						/>
						<line
							className="line-left"
							fill="none"
							stroke="#333333"
							strokeWidth={2}
							strokeLinecap="round"
							strokeLinejoin="round"
							x1={21}
							y1={8}
							x2={24}
							y2="11.1"
						/>
					</svg>
				</div>
				<div className="cell small-10 padding-left-1">
					<p>
						<b>Other people want this.</b> Over {"{"}
						InCartNumber
						{"}"} people have this in their carts right now.
					</p>
				</div>
			</div>
		</div>
	);
};

export default createFragmentContainer(AddToCart, {
	shop: graphql`
		fragment AddToCart_shop on Shop {
			shopName
			shopImage
		}
	`,
	Product: graphql`
		fragment AddToCart_Product on Product {
			id
			productImage
			productQuantity
			productPrice
			productDescription {
				productName
			}
		}
	`
});
