import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import environment from "../../../../schema/environment";
const mutation = graphql`
	mutation DeleteShopMutation(
		$input1: DeleteShopInfoInput!
		$input2: DeleteShopInput!
	) {
		deleteShopInfo(input: $input1) {
			deletedId
		}
		deleteShop(input: $input2) {
			deletedId
		}
	}
`;

function DeleteShopMutation(
	shopInfoId: string,
	shopId: string,
	viewerId: string
) {
	const variables = {
		input1: {
			id: shopInfoId,
			clientMutationId: ""
		},
		input2: {
			id: shopId,
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const viewerProxy = oldStore.get(viewerId || "");
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"ShopList_allShops"
			);
			if (connection) {
				ConnectionHandler.deleteNode(connection, shopId);
			}
		},
		updater: (newStore) => {
			const deleteShopField = newStore.getRootField("deleteShop");
			if (deleteShopField) {
				const deletedId = deleteShopField.getValue("deletedId");

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"ShopList_allShops"
				);
				if (connection) {
					ConnectionHandler.deleteNode(connection, deletedId);
				}
			}
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default DeleteShopMutation;
