import React, {Component} from "react";
import {QueryRenderer, graphql} from "react-relay";
import {withRouter, RouteComponentProps} from "react-router-dom";
import environment from "../../schema/environment";
import AddToCart from "./AddToCart/AddToCart";
import Overview from "./Overview/Overview";
import PaymentShipping from "./PaymentShipping/PaymentShipping";
import RelatedProducts from "./RelatedProducts/RelatedProducts";
import Reviews from "./Reviews/Reviews";
import ShopItems from "./ShopItems/ShopItems";

class ProductPage extends Component {
	props: RouteComponentProps;

	constructor(props: RouteComponentProps) {
		super(props);
		this.props = props;
	}

	componentDidMount = () => {
		window.scrollTo(0, 0);
	};

	render() {
		const ProductPageViewerQuery = graphql`
			query ProductPageViewerQuery($productId: ID!) {
				viewer {
					Product(id: $productId) {
						...AddToCart_Product
						...Reviews_Product
						shop {
							...AddToCart_shop
							...ShopItems_shop
						}
						productDescription {
							...Overview_productDescription
						}
					}
				}
			}
		`;

		return (
			<QueryRenderer
				environment={environment}
				query={ProductPageViewerQuery}
				variables={{productId: (this.props.match.params as any).id}}
				render={({error, props}) => {
					if (error) {
						return (
							<p>
								<b>Error: {error.message}</b>
							</p>
						);
					} else if (!props) {
						return (
							<p>
								<b>Loading...</b>
							</p>
						);
					} else {
						return (
							<div className="grid-x align-center" id="ProductPage">
								<div className="grid-x grid-padding-x padding-2 large-div medium-up-2">
									<div className="cell">
										<AddToCart
											Product={props.viewer.Product}
											shop={props.viewer.Product.shop}
										/>
										<PaymentShipping />
										<Reviews Product={props.viewer.Product} />
									</div>
									<div className="cell">
										<Overview
											productDescription={
												props.viewer.Product.productDescription
											}
										/>
										<RelatedProducts />
										<ShopItems shop={props.viewer.Product.shop} />
									</div>
								</div>
							</div>
						);
					}
				}}
			/>
		);
	}
}

export default withRouter(ProductPage);
