import React, {Component} from "react";
import {createFragmentContainer, graphql} from "react-relay";
import ShopDeleteMutation from "./DeleteShopMutation";

interface IShopProps {
	viewer: {
		id: string;
	};
	shop: {
		id: string;
		shopName: string;
		shopImage: string;
		shopInfo: {
			id: string;
			shopAddress: string;
			shopUrl: string;
		};
	};
}
const Shop = (props: IShopProps) => {
	const shopDeleteHandler = () => {
		ShopDeleteMutation(props.shop.shopInfo.id, props.shop.id, props.viewer.id);
	};

	return (
		<div className="grid-x bordered padding-1" style={{maxWidth: 500}}>
			<div className="cell">
				id: <b>{props.shop.id}</b>
			</div>
			<div className="cell">
				shopName: <b>{props.shop.shopName}</b>
			</div>
			<div className="cell">
				shopAddress: <b>{props.shop.shopInfo.shopAddress}</b>
			</div>
			<div className="cell">
				shopUrl: <b>{props.shop.shopInfo.shopUrl || ""}</b>
			</div>
			<div
				className="cell margin-top-1"
				style={{
					backgroundImage: `url(${props.shop.shopImage})`,
					backgroundPosition: "center center",
					backgroundRepeat: "no-repeat",
					backgroundSize: "cover",
					minHeight: 300,
					minWidth: 300
				}}
			/>
			<div className="cell margin-top-1 text-center">
				<button type="button" className="button" onClick={shopDeleteHandler}>
					Delete
				</button>
			</div>
		</div>
	);
};

export default createFragmentContainer(Shop, {
	shop: graphql`
		fragment Shop_shop on Shop {
			id
			shopName
			shopImage
			shopInfo {
				id
				shopAddress
				shopUrl
			}
		}
	`,
	viewer: graphql`
		fragment Shop_viewer on Viewer {
			id
		}
	`
});
