import React, {Component} from "react";
import {createFragmentContainer, graphql} from "react-relay";
import Review from "./Review";

interface IReviewsProps {
	Product: {
		review: {
			edges: [
				{
					node: {
						createdAt: string;
						customer: {
							customerFirstname: string;
							customerLastname: string;
							customerImage: string;
						};
						reviewDescription: {
							reviewText: string;
						};
					};
				}
			];
		};
	};
}

interface IReviewsState {
	moreReviews: boolean;
}
class Reviews extends Component {
	props: IReviewsProps;
	state: IReviewsState;

	constructor(props: IReviewsProps) {
		super(props);
		this.props = props;
		this.state = {
			moreReviews: false
		};
	}

	componentDidMount = () => {
		($("#ProductPage") as any).foundation();
		const reviewTogglers = document.querySelectorAll(
			".review-toggler"
		) as NodeListOf<HTMLAnchorElement>;
		reviewTogglers.forEach((a) => {
			a.addEventListener("click", (e) => {
				const text = a.text.toLocaleLowerCase().trim();
				if (text === "read more +") {
					a.text = "Read less -";
				} else if (text === "read less -") {
					a.text = "Read more +";
				}
			});
		});
	};

	moreReviewsToggler = () => {
		this.setState((prevState: IReviewsState) => {
			return {
				moreReviews: !prevState.moreReviews
			};
		});
	};

	render() {
		return (
			<div className="optional-background padding-1" id="Reviews">
				<p>
					<b>Reviews</b> <i className="fas fa-star" />
					<i className="fas fa-star" />
					<i className="fas fa-star" />
					<i className="fas fa-star" />
					<i className="fas fa-star" />
					<span className="subheader">(2,593)</span>
				</p>
				{this.props.Product.review.edges.map(({node}, idx) => {
					return (
						<div
							className={idx === 1 ? "hide padding-vertical-1" : ""}
							id={idx === 1 ? "reviews" : ""}
							key={idx}
							data-toggler={idx === 1 ? ".hide" : ""}>
							<Review idx={idx} review={node} />
							<p>
								{idx === 0 && (
									<a
										className="underline margin-vertical-2"
										onClick={this.moreReviewsToggler}
										data-toggle="reviews">
										{!this.state.moreReviews
											? "More reviews +"
											: "Less reviews -"}
									</a>
								)}
								{idx === this.props.Product.review.edges.length - 1 && (
									<a href="#" className="button expanded">
										Read all reviews (2,593)
									</a>
								)}
							</p>
						</div>
					);
				})}
			</div>
		);
	}
}

export default createFragmentContainer(Reviews, {
	Product: graphql`
		fragment Reviews_Product on Product {
			review {
				edges {
					node {
						...Review_review
					}
				}
			}
		}
	`
});
