import React, {Component} from "react";
import CreateCustomerMutation from "./CreateCustomerMutation";
import {QueryRenderer, graphql} from "react-relay";
import {withRouter, RouteComponentProps} from "react-router-dom";
import crypto from "crypto";
import environment from "../../../../schema/environment";

interface ICustomerCreateState {
	customerFirstname: string;
	customerLastname: string;
	customerImage: string;
	customerGender: boolean;
	customerDob: string;
	customerPhone: string;
	customerEmailAddress: string;
	customerPassword: string;
	customerNewsletter: boolean;
}
class CustomerCreate extends Component {
	props: RouteComponentProps;
	state: ICustomerCreateState;

	constructor(props: RouteComponentProps) {
		super(props);
		this.props = props;
		this.state = {
			customerFirstname: "Abraham",
			customerLastname: "Lincoln",
			customerImage: "",
			customerGender: true,
			customerDob: "1990-02-10",
			customerPhone: "0987654321",
			customerEmailAddress: "abe.lincoln@example.com",
			customerPassword: "somerandompassword",
			customerNewsletter: true
		};
	}

	inputHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
		this.setState({
			[e.target.name]:
				e.target.type !== "checkbox" ? e.target.value : e.target.checked
		});
	};

	customerCreateHandler = (viewerId: string) => {
		const updatedState = Object.assign({}, this.state);
		updatedState.customerPassword = crypto
			.createHash("sha512")
			.update(updatedState.customerPassword)
			.digest("hex");
		CreateCustomerMutation(updatedState, viewerId, () => {
			this.props.history.push("/create/listcustomer");
		});
	};

	render() {
		const CustomerCreateViewerQuery = graphql`
			query CustomerCreateViewerQuery {
				viewer {
					id
				}
			}
		`;

		return (
			<QueryRenderer
				environment={environment}
				query={CustomerCreateViewerQuery}
				variables={{}}
				render={({error, props}) => {
					if (error) {
						return (
							<p>
								<b>Error: {error.message}</b>
							</p>
						);
					} else if (!props) {
						return (
							<p>
								<b>Loading...</b>
							</p>
						);
					} else {
						return (
							<div className="grid-x align-center margin-vertical-1">
								<div
									className="grid-x shadow padding-1"
									style={{maxWidth: 300}}>
									<div className="cell grid-x">
										customerFirstname:
										<input
											required
											type="text"
											name="customerFirstname"
											value={this.state.customerFirstname}
											className="cell margin-bottom-1"
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerLastname:
										<input
											required
											type="text"
											name="customerLastname"
											value={this.state.customerLastname}
											className="cell margin-bottom-1"
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerImage:
										<label className="cell margin-bottom-1">
											<input
												required
												type="text"
												name="customerImage"
												value={this.state.customerImage}
												onChange={this.inputHandler}
											/>
										</label>
									</div>
									<div className="cell grid-x">
										customerGender:
										<label className="cell margin-bottom-1">
											<input
												required
												type="checkbox"
												name="customerGender"
												checked={this.state.customerGender}
												onChange={this.inputHandler}
											/>
											Male?
										</label>
									</div>
									<div className="cell grid-x">
										customerDob:
										<input
											required
											type="date"
											name="customerDob"
											value={this.state.customerDob}
											className="cell margin-bottom-1"
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerPhone:
										<input
											required
											type="number"
											name="customerPhone"
											value={this.state.customerPhone}
											className="cell margin-bottom-1"
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerEmailAddress:
										<input
											required
											type="email"
											name="customerEmailAddress"
											value={this.state.customerEmailAddress}
											className="cell margin-bottom-1"
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerPassword:
										<input
											required
											type="password"
											name="customerFirstname"
											value={this.state.customerPassword}
											className="cell margin-bottom-1"
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerNewsletter:
										<label className="cell margin-bottom-1">
											<input
												required
												type="checkbox"
												name="customerNewsletter"
												checked={this.state.customerNewsletter}
												onChange={this.inputHandler}
											/>
											Receive?
										</label>
									</div>
									<div className="cell text-right">
										<button
											type="button"
											className="button"
											onClick={(e) => {
												this.customerCreateHandler(props.viewer.id);
											}}>
											Submit
										</button>
									</div>
								</div>
							</div>
						);
					}
				}}
			/>
		);
	}
}

export default withRouter(CustomerCreate);
