import React, {Component} from "react";
import {QueryRenderer, graphql} from "react-relay";
import {withRouter, RouteComponentProps} from "react-router-dom";
import environment from "../../../../schema/environment";
import CreateProductMutation from "./CreateProductMutation";

interface IProductCreateState {
	productName: string;
	productOverview: string;
	productDescription: string;
	productModel: string;
	productImage: string;
	productPrice: string;
	productQuantity: number;
	productDateAvailable: string;
	productWeight: number;
	productStatus: boolean;
}
interface IUpdatedState extends IProductCreateState {
	shopId: string;
	shopName: string;
	taxClassId: string;
	taxClassTitle: string;
	languageId: string;
}
class ProductCreate extends Component {
	props: RouteComponentProps;
	state: IProductCreateState;

	constructor(props: any) {
		super(props);
		this.props = props;
		this.state = {
			productName:
				"Super Chunky Knit Blanket  Pure Merino Chunky Throw Blanket - Giant Knit Blanket - Huge Stitch Size",
			productOverview: "123",
			productDescription: "123",
			productModel: "Ford GT",
			productImage:
				"https://cdn-images-1.medium.com/max/2000/0*p_mSrJU2L90IXDSi.jpg",
			productPrice: "1000",
			productQuantity: 10,
			productDateAvailable: "2020-12-31",
			productWeight: 100,
			productStatus: false
		};
	}

	inputHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
		this.setState({
			[e.target.name]:
				e.target.type !== "checkbox" ? e.target.value : e.target.checked
		});
	};

	createProductHandler = (viewerId: string) => {
		const updatedState = Object.assign({}, this.state) as IUpdatedState;
		const shopSelect = document.querySelector(
			"#shopSelect"
		) as HTMLSelectElement;
		const languageSelect = document.querySelector(
			"#languageSelect"
		) as HTMLSelectElement;
		const taxSelect = document.querySelector("#taxSelect") as HTMLSelectElement;
		if (shopSelect && taxSelect) {
			const selectedShop = shopSelect.options[shopSelect.selectedIndex];
			const selectedLanguage =
				languageSelect.options[languageSelect.selectedIndex];
			const selectedTax = taxSelect.options[taxSelect.selectedIndex];

			updatedState.shopId = selectedShop.value;
			updatedState.shopName = selectedShop.text;
			updatedState.languageId = selectedLanguage.value;
			updatedState.taxClassId = selectedTax.value;
			updatedState.taxClassTitle = selectedTax.text;
			CreateProductMutation(updatedState, viewerId, () => {
				this.props.history.push("/create/listproduct");
			});
		}
	};

	render() {
		const ProductCreateViewerQuery = graphql`
			query ProductCreateViewerQuery {
				viewer {
					id
					allShops(first: 10, orderBy: shopName_ASC) {
						edges {
							node {
								id
								shopName
							}
						}
					}
					allTaxClasses(first: 10, orderBy: taxClassTitle_ASC) {
						edges {
							node {
								id
								taxClassTitle
							}
						}
					}
					allLanguages(orderBy: name_ASC) {
						edges {
							node {
								id
								name
							}
						}
					}
				}
			}
		`;

		return (
			<QueryRenderer
				environment={environment}
				query={ProductCreateViewerQuery}
				variables={{}}
				render={({error, props}) => {
					if (error) {
						return (
							<p>
								<b>Error: {error.message}</b>
							</p>
						);
					} else if (!props) {
						return (
							<p>
								<b>Loading...</b>
							</p>
						);
					} else {
						return (
							<div className="grid-x align-center margin-vertical-1">
								<div
									className="grid-x shadow padding-1"
									style={{maxWidth: 300}}>
									<div className="cell grid-x">
										productName:
										<input
											required
											type="text"
											name="productName"
											value={this.state.productName}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									<div className="cell grid-x">
										productOverview:
										<input
											required
											type="text"
											name="productOverview"
											value={this.state.productOverview}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									<div className="cell grid-x">
										productDescription:
										<input
											required
											type="text"
											name="productDescription"
											value={this.state.productDescription}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									<div className="cell grid-x">
										productModel:
										<input
											required
											type="text"
											name="productModel"
											value={this.state.productModel}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									<div className="cell grid-x">
										productImage:
										<input
											required
											type="text"
											name="productImage"
											value={this.state.productImage}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									{this.state.productImage && (
										<div className="cell">
											<img src={this.state.productImage} alt="Example image" />
										</div>
									)}
									<div className="cell grid-x">
										productPrice:
										<input
											required
											type="number"
											name="productPrice"
											step="0.01"
											value={this.state.productPrice}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									<div className="cell grid-x">
										productQuantity:
										<input
											required
											type="number"
											name="productQuantity"
											value={this.state.productQuantity}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									<div className="cell grid-x">
										productDateAvailable:
										<input
											required
											type="date"
											name="productDateAvailable"
											value={this.state.productDateAvailable}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									<div className="cell grid-x">
										productWeight:
										<input
											required
											type="number"
											name="productWeight"
											value={this.state.productWeight}
											onChange={this.inputHandler}
											className="cell margin-bottom-1"
										/>
									</div>
									<div className="cell grid-x">
										productStatus:
										<label className="cell margin-bottom-1">
											<input
												required
												type="checkbox"
												name="productStatus"
												checked={this.state.productStatus}
												onChange={this.inputHandler}
											/>
											Available?
										</label>
									</div>
									<div className="cell grid-x">
										[Belong to] language-name:
										<select id="languageSelect">
											{props.viewer.allLanguages.edges.map(
												(
													{node}: {node: {id: string; name: string}},
													idx: number
												) => {
													return (
														<option key={idx} value={node.id}>
															{node.name}
														</option>
													);
												}
											)}
										</select>
									</div>
									<div className="cell grid-x">
										[Belong to] shopName:
										<select id="shopSelect">
											{props.viewer.allShops.edges.map(
												(
													{node}: {node: {id: string; shopName: string}},
													idx: number
												) => {
													return (
														<option key={idx} value={node.id}>
															{node.shopName}
														</option>
													);
												}
											)}
										</select>
									</div>
									<div className="cell grid-x">
										[Belong to] taxClassTitle:
										<select id="taxSelect">
											{props.viewer.allTaxClasses.edges.map(
												(
													{node}: {node: {id: string; taxClassTitle: string}},
													idx: number
												) => {
													return (
														<option key={idx} value={node.id}>
															{node.taxClassTitle}
														</option>
													);
												}
											)}
										</select>
									</div>
									<div className="cell text-right">
										<button
											type="button"
											className="button"
											onClick={(e) => {
												this.createProductHandler(props.viewer.id);
											}}>
											Submit
										</button>
									</div>
								</div>
							</div>
						);
					}
				}}
			/>
		);
	}
}

export default withRouter(ProductCreate);
