import React, {Component} from "react";
import {Link} from "react-router-dom";
import {createFragmentContainer, graphql} from "react-relay";

interface IShopItemsProps {
	shop: {
		shopName: string;
		shopImage: string;
		shopInfo: {
			shopAddress: string;
		};
		product: {
			edges: [
				{
					node: {
						productPrice: number;
						productImage: string;
						productDescription: {
							productName: string;
						};
					};
				}
			];
		};
	};
}
const ShopItems = (props: IShopItemsProps) => {
	return (
		<div className="optional-background padding-1" id="ShopItems">
			<p>
				<b>
					More from the shop{" "}
					<Link to="/shop/test" className="underline" href="#">
						{props.shop.shopName}
					</Link>
				</b>
			</p>
			<div className="grid-x align-middle padding-bottom-1">
				<div className="cell small-3 medium-2">
					<Link to="/shop/test">
						<img
							className="thumbnail img-with-shadow"
							src={props.shop.shopImage}
							alt="Shop's products"
						/>
					</Link>
				</div>
				<div className="cell small-9 medium-10 padding-left-1">
					<small className="p">{props.shop.shopInfo.shopAddress}</small>
				</div>
			</div>
			<div className="grid-x grid-padding-x medium-up-2">
				{props.shop.product.edges.map(({node}, idx) => {
					return (
						<Link
							to="/product/test"
							className="cell"
							key={idx}
							title="Cuddly Ultra Soft Chunky Merino Throw Blanket -Super Chunky Knit Merino Wool 40&quot; x 63&quot; Throw Blanket Giant Knit, Bulky Knit Blanket">
							<img src={node.productImage} alt="Other product 1" />
							<div className="truncate-wrapper">
								<small className="text-truncate normal">
									{node.productDescription.productName}
								</small>
							</div>
							<p>
								<small>
									<b>USD {node.productPrice}</b>
								</small>
							</p>
						</Link>
					);
				})}
			</div>
			<a href="#" className="button expanded margin-vertical-1">
				Shop all items (127)
			</a>
		</div>
	);
};

export default createFragmentContainer(ShopItems, {
	shop: graphql`
		fragment ShopItems_shop on Shop {
			shopName
			shopImage
			shopInfo {
				shopAddress
			}
			product(last: 4) {
				edges {
					node {
						productPrice
						productImage
						productDescription {
							productName
						}
					}
				}
			}
		}
	`
});
