import React, {Component} from "react";
import {createFragmentContainer, graphql} from "react-relay";
import {Link} from "react-router-dom";
import Order from "./Order";

interface IOrderListProps {
	viewer: {
		id: string;
		allOrders: {
			edges: [
				{
					node: {
						id: string;
						customerName: string;
						customerStreetAddress: string;
						customerCity: string;
						customerPostcode: number;
						customerState: string;
						customerCountry: string;
						customerPhone: string;
						customerEmailAddress: string;
						deliveryName: string;
						deliveryStreetAddress: string;
						deliveryCity: string;
						deliveryPostcode: number;
						deliveryState: string;
						deliveryCountry: string;
						paymentMethod: string;
						ccType: string;
						ccOwner: string;
						ccNumber: number;
						ccExpires: string;
						shippingCost: number;
						shippingMethod: string;
						orderFinishedDate: string;
						comments: string;
						currency: string;
						currencyValue: number;
						createdAt: string;
						updatedAt: string;
						customer: {
							customerFirstname: string;
							customerLastname: string;
						};
						orderProduct: {
							edges: [
								{
									node: {
										id: string;
										product: {
											productDescription: {
												productName: string;
											};
										};
									};
								}
							];
						};
					};
				}
			];
		};
	};
}
const OrderList = (props: IOrderListProps) => {
	return (
		<div className="grid-x align-center padding-1">
			<div className="grid-x" style={{maxWidth: 1000}}>
				<div className="cell text-center">
					<Link to="/create/createorder" className="button margin-bottom-1">
						Create new Order
					</Link>
				</div>
				<div className="cell grid-x small-up-2 medium-up-3">
					{props.viewer.allOrders.edges.map(({node}, idx) => {
						return (
							<div className="cell" key={idx}>
								<Order order={node} viewer={props.viewer} />
							</div>
						);
					})}
				</div>
			</div>
		</div>
	);
};

export default createFragmentContainer(OrderList, {
	viewer: graphql`
		fragment OrderList_viewer on Viewer {
			...Order_viewer
			allOrders(first: 10, orderBy: createdAt_ASC)
				@connection(key: "OrderList_allOrders", filters: []) {
				edges {
					node {
						...Order_order
					}
				}
			}
		}
	`
});
