import React from "react";
import {createFragmentContainer, graphql} from "react-relay";
import DeleteCustomerMutation from "./DeleteCustomerMutation";

interface ICustomerProps {
	viewer: {
		id: string;
	};
	customer: {
		id: string;
		customerFirstname: string;
		customerLastname: string;
		customerGender: boolean;
		customerImage: string;
		customerDob: string;
		customerPhone: string;
		customerEmailAddress: string;
		customerPassword: string;
		customerNewsletter: boolean;
	};
}
const Customer = (props: ICustomerProps) => {
	const deleteCustomerHandler = () => {
		DeleteCustomerMutation(props.customer.id, props.viewer.id);
	};

	return (
		<div className="grid-x shadow padding-1">
			<div className="cell text-right">
				<button
					type="button"
					className="button hollow"
					onClick={deleteCustomerHandler}>
					Delete
				</button>
			</div>
			<div className="cell grid-x">
				id:
				<small className="cell margin-bottom-1">
					<b>{props.customer.id}</b>
				</small>
			</div>
			<div className="cell grid-x">
				customerFirstname:
				<small className="cell margin-bottom-1">
					<b>{props.customer.customerFirstname}</b>
				</small>
			</div>
			<div className="cell grid-x">
				customerLastname:
				<small className="cell margin-bottom-1">
					<b>{props.customer.customerLastname}</b>
				</small>
			</div>
			<div className="cell grid-x">
				customerImage:
				<div className="cell margin-bottom-1 text-center">
					<img src={props.customer.customerImage} alt="Customer avatar" />
				</div>
			</div>
			<div className="cell grid-x">
				customerGender:
				<small className="cell margin-bottom-1">
					<b>{props.customer.customerGender === true ? "Male" : "Female"}</b>
				</small>
			</div>
			<div className="cell grid-x">
				customerDob:
				<small className="cell margin-bottom-1">
					<b>{props.customer.customerDob}</b>
				</small>
			</div>
			<div className="cell grid-x">
				customerPhone:
				<small className="cell margin-bottom-1">
					<b>{props.customer.customerPhone}</b>
				</small>
			</div>
			<div className="cell grid-x">
				customerEmailAddress:
				<small className="cell margin-bottom-1">
					<b>{props.customer.customerEmailAddress}</b>
				</small>
			</div>
			<div className="cell grid-x">
				customerPassword:
				<small className="cell margin-bottom-1">
					<b>
						{props.customer.customerPassword.slice(0, 9).replace(/./g, "*")}
					</b>
				</small>
			</div>
			<div className="cell grid-x">
				customerNewsletter:
				<small className="cell margin-bottom-1">
					<b>
						{props.customer.customerNewsletter === true
							? "Receiving"
							: "Not receiving"}
					</b>
				</small>
			</div>
		</div>
	);
};

export default createFragmentContainer(Customer, {
	customer: graphql`
		fragment Customer_customer on Customer {
			id
			customerFirstname
			customerLastname
			customerImage
			customerGender
			customerDob
			customerPhone
			customerEmailAddress
			customerPassword
			customerNewsletter
		}
	`,
	viewer: graphql`
		fragment Customer_viewer on Viewer {
			id
		}
	`
});
