import React, {Component} from "react";
import {QueryRenderer, graphql} from "react-relay";
import {withRouter, RouteComponentProps} from "react-router-dom";
import CreateShopMutation from "./CreateShopMutation";
import environment from "../../../../schema/environment";

interface IShopCreateProps extends RouteComponentProps {
	viewer: {
		id: string;
		allLanguages: {
			edges: [
				{
					node: {
						id: string;
						name: string;
					};
				}
			];
		};
	};
}
interface IShopCreateState {
	shopName: string;
	shopImage: string;
	shopAddress: string;
	shopUrl: string;
}
interface IUpdatedState extends IShopCreateState {
	languageId: string;
}
class ShopCreate extends Component {
	props: IShopCreateProps;
	state: IShopCreateState;

	constructor(props: IShopCreateProps) {
		super(props);
		this.props = props;
		this.state = {
			shopName: "Pain Shop",
			shopImage:
				"https://en.paindesucre.com/media/storelocator/thumbnail/PICTbordeaux8.jpg",
			shopAddress: "445 Mount Eden Road, Mount Eden, Auckland",
			shopUrl: "https://www.nzpost.co.nz"
		};
	}

	shopCreateHandler = (viewerId: string) => {
		const updatedState = Object.assign({}, this.state) as IUpdatedState;
		const languageSelect = document.querySelector(
			"#languageSelect"
		) as HTMLSelectElement;
		if (languageSelect) {
			updatedState.languageId =
				languageSelect.options[languageSelect.selectedIndex].value;
			CreateShopMutation(updatedState, viewerId, () => {
				this.props.history.push("/create/listshop");
			});
		}
	};

	render() {
		const ShopCreateViewerQuery = graphql`
			query ShopCreateViewerQuery {
				viewer {
					id
					allLanguages(orderBy: name_ASC) {
						edges {
							node {
								id
								name
							}
						}
					}
				}
			}
		`;

		return (
			<QueryRenderer
				environment={environment}
				query={ShopCreateViewerQuery}
				variables={{}}
				render={({error, props}) => {
					if (error) {
						return (
							<p>
								<b>Error: {error.message}</b>
							</p>
						);
					} else if (!props) {
						return (
							<p>
								<b>Loading...</b>
							</p>
						);
					} else {
						return (
							<div className="grid-x align-center margin-vertical-1">
								<div className="shadow padding-1" style={{maxWidth: 300}}>
									<div className="cell">
										shopName:
										<input
											required
											type="text"
											value={this.state.shopName}
											placeholder="Type in shop name..."
											onChange={(e) => {
												this.setState({shopName: e.target.value});
											}}
										/>
									</div>
									<div className="cell">
										shopAddress:
										<input
											required
											type="text"
											value={this.state.shopAddress}
											onChange={(e) => {
												this.shopCreateHandler(e.target.value);
											}}
										/>
									</div>
									<div className="cell">
										shopUrl:
										<input
											type="text"
											value={this.state.shopAddress}
											onChange={(e) => {
												this.shopCreateHandler(e.target.value);
											}}
										/>
									</div>
									<div className="cell">
										[Belong to] language-name:
										<select id="languageSelect">
											{props.viewer.allLanguages.edges.map(
												(
													{node}: {node: {id: string; name: string}},
													idx: number
												) => {
													return (
														<option key={idx} value={node.id}>
															{node.name}
														</option>
													);
												}
											)}
										</select>
									</div>
									<div className="cell">
										shopImage:
										<input
											required
											type="text"
											value={this.state.shopImage}
											placeholder="Type in shop image..."
											onChange={(e) => {
												this.setState({shopImage: e.target.value});
											}}
										/>
									</div>
									{this.state.shopImage && (
										<div className="cell text-center">
											<img
												src={this.state.shopImage}
												alt="Some image"
												className="thumbnail"
												style={{maxWidth: 200}}
											/>
										</div>
									)}
									<div className="cell text-right">
										<button
											type="button"
											className="button"
											onClick={(e) => {
												this.shopCreateHandler(props.viewer.id);
											}}>
											Submit
										</button>
									</div>
								</div>
							</div>
						);
					}
				}}
			/>
		);
	}
}

export default withRouter(ShopCreate);
