import React, {Component} from "react";
import CoverTitle from "./CoverTitle/CoverTitle";
import StickyContent from "./StickyContent/StickyContent";

class SellPage extends Component {
	componentDidMount = () => {
		($("#SellPage") as any).foundation();
		window.scrollTo(0, 0);
	};

	render() {
		return (
			<div id="SellPage">
				<CoverTitle />
				<StickyContent />
			</div>
		);
	}
}

export default SellPage;
