import React, {Component} from "react";
import ShopList from "./ShopList";
import {QueryRenderer, graphql} from "react-relay";
import environment from "../../../../schema/environment";

const ShopAppAllShopsQuery = graphql`
	query ShopAppAllShopsQuery {
		viewer {
			...ShopList_viewer
		}
	}
`;

const App = () => {
	return (
		<QueryRenderer
			environment={environment}
			variables={{}}
			query={ShopAppAllShopsQuery}
			render={({error, props}) => {
				if (error) {
					return (
						<p>
							<b>Error: {error.message}</b>
						</p>
					);
				} else if (!props) {
					return (
						<p>
							<b>Loading...</b>
						</p>
					);
				} else {
					return (
						<div className="padding-horizontal-3 padding-vertical-1 grid-x align-center">
							<ShopList viewer={props.viewer} />
						</div>
					);
				}
			}}
		/>
	);
};

export default App;
