import React, {Component} from "react";
import {createFragmentContainer, graphql} from "react-relay";
import Customer from "./Customer";
import {Link} from "react-router-dom";

interface ICustomerListProps {
	viewer: {
		id: string;
		allCustomers: {
			edges: [
				{
					node: {
						id: string;
						customerFirstname: string;
						customerLastname: string;
						customerImage: string;
						customerGender: boolean;
						customerDob: string;
						customerPhone: string;
						customerEmailAddress: string;
						customerPassword: string;
						customerNewsletter: boolean;
					};
				}
			];
		};
	};
}
const CustomerList = (props: ICustomerListProps) => {
	return (
		<div className="grid-x align-center padding-1">
			<div className="grid-x" style={{maxWidth: 1000}}>
				<div className="cell text-center">
					<Link to="/create/createcustomer" className="button margin-bottom-1">
						Create new Customer
					</Link>
				</div>
				<div className="cell grid-x small-up-2 medium-up-3">
					{props.viewer.allCustomers.edges.map(({node}, idx) => {
						return (
							<div className="cell" key={idx}>
								<Customer customer={node} viewer={props.viewer} />
							</div>
						);
					})}
				</div>
			</div>
		</div>
	);
};

export default createFragmentContainer(CustomerList, {
	viewer: graphql`
		fragment CustomerList_viewer on Viewer {
			...Customer_viewer
			allCustomers(first: 10, orderBy: customerLastname_ASC)
				@connection(key: "CustomerList_allCustomers", filters: []) {
				edges {
					node {
						...Customer_customer
					}
				}
			}
		}
	`
});
