import React, {Component} from "react";
import {Link} from "react-router-dom";
import {createFragmentContainer, graphql} from "react-relay";
import Product from "./Product";

interface IProductListProps {
	viewer: {
		id: string;
		allProducts: {
			edges: [
				{
					node: {
						id: string;
						productModel: string;
						productImage: string;
						productPrice: number;
						productQuantity: number;
						productDateAvailable: string;
						productWeight: number;
						productStatus: boolean;
						createdAt: string;
						updatedAt: string;
						productDescription: {
							id: string;
							productName: string;
							productOverview: string;
							productDescription: string;
						};
						shop: {
							shopName: string;
						};
						taxClass: {
							taxClassTitle: string;
						};
					};
				}
			];
		};
	};
}
const ProductList = (props: IProductListProps) => {
	return (
		<div className="grid-x align-center padding-1">
			<div className="grid-x" style={{maxWidth: 1000}}>
				<div className="cell text-center">
					<Link className="button margin-bottom-1" to="/create/createproduct">
						Create new Product
					</Link>
				</div>
				<div className="cell grid-x small-up-2 medium-up-3">
					{props.viewer.allProducts.edges.map(({node}, idx) => {
						return (
							<div className="cell" key={idx}>
								<Product product={node} viewer={props.viewer} />
							</div>
						);
					})}
				</div>
			</div>
		</div>
	);
};

export default createFragmentContainer(ProductList, {
	viewer: graphql`
		fragment ProductList_viewer on Viewer {
			...Product_viewer
			allProducts(first: 10, orderBy: productModel_ASC)
				@connection(key: "ProductList_allProducts", filters: []) {
				edges {
					node {
						...Product_product
					}
				}
			}
		}
	`
});
