import React, {Component} from "react";
import uuid from "uuid";
import ShopInCart from "./ShopInCart/ShopInCart";

interface ICartCheckoutProps {
	btnText: string;
}
interface ICartCheckoutState {
	items?: {
		shopName: string;
		products: {
			id: string;
			productImage: string;
			productPrice: number;
			productQuantity: number;
			productDescription: {
				productName: string;
			};
		}[];
	}[];
}
class CartCheckout extends Component {
	props: ICartCheckoutProps;
	state: ICartCheckoutState;

	constructor(props: ICartCheckoutProps) {
		super(props);
		this.props = props;
		// localStorage.removeItem("cartItems");
		const cartItems = localStorage.getItem("cartItems");
		if (cartItems) {
			this.state = JSON.parse(cartItems);
		} else {
			this.state = {};
		}
	}

	componentDidMount = () => {
		document.addEventListener("localStorageSaved", (e: any) => {
			this.setState(e.detail);
		});
	};

	removeItemHandler = (id: string) => {
		if (this.state.items) {
			this.setState((prevState: ICartCheckoutState) => {
				let updatedState = Object.assign({}, prevState);
				updatedState.items = (updatedState.items as any).filter(
					(currentItem) => {
						currentItem.products = currentItem.products.filter(
							(currentProduct) => {
								return currentProduct.id !== id;
							}
						);
						return currentItem.products.length > 0;
					}
				);
				localStorage.setItem("cartItems", JSON.stringify(updatedState));
				return updatedState;
			});
		}
	};

	saveOnLocal = (id: string, quantity: number) => {
		if (this.state.items) {
			localStorage.setItem("cartItems", JSON.stringify(this.state));
			this.setState((prevState: ICartCheckoutState) => {
				const updatedState = Object.assign({}, prevState);
				(updatedState.items as any).find((currentItem) => {
					const productFound = currentItem.products.find((currentProduct) => {
						return currentProduct.id === id;
					});
					if (productFound) {
						productFound.productQuantity = quantity;
					}
					return typeof productFound !== "undefined";
				});
				return updatedState;
			});
		}
	};

	render() {
		let itemsTotal = 0;
		let priceTotal = 0;
		let ItemComponents = [<p />];

		if (this.state.items) {
			const total = this.state.items.map((currentItem) => {
				const itemsTotal = currentItem.products.reduce((total, currentShop) => {
					return total + currentShop.productQuantity;
				}, 0);
				const priceTotal = currentItem.products.reduce(
					(total, currentProduct) => {
						return (
							total +
							currentProduct.productPrice * currentProduct.productQuantity
						);
					},
					0
				);
				const priceAverage = priceTotal / itemsTotal;

				return {
					[currentItem.shopName]: {
						itemsTotal,
						priceTotal,
						priceAverage
					}
				};
			});

			itemsTotal = total.reduce((total, currentShop) => {
				return total + currentShop[Object.keys(currentShop)[0]].itemsTotal;
			}, 0);
			priceTotal = total.reduce((total, currentShop) => {
				return total + currentShop[Object.keys(currentShop)[0]].priceTotal;
			}, 0);

			ItemComponents = this.state.items.map((currentItem, idx) => {
				return (
					<ShopInCart
						item={currentItem}
						idx={idx}
						total={total}
						key={uuid.v4()}
						removeItemHandler={this.removeItemHandler}
						saveOnLocal={this.saveOnLocal}
					/>
				);
			});
		}

		return (
			<div>
				<button
					type="button"
					className="grid-x button hollow border-none"
					data-toggle="modalCart">
					{itemsTotal > 0 ? (
						<div className="cell grid-x small-up-2">
							<i className="cell fas fa-shopping-cart padding-bottom-1" />
							<div className="cell">
								<span className="badge warning">{itemsTotal}</span>
							</div>
						</div>
					) : (
						<div className="cell grid-x">
							<i className="cell fas fa-shopping-cart padding-bottom-1" />
						</div>
					)}
					<span className="cell">{this.props.btnText}</span>
				</button>
				<div
					className="reveal large"
					id="modalCart"
					data-reveal
					data-close-on-click="true">
					<br />
					{itemsTotal > 0 ? (
						<form data-abide noValidate>
							<p className="lead padding-bottom-1">
								<b>Items in your cart: {itemsTotal}</b>
							</p>
							<div className="grid-x grid-padding-x">
								{ItemComponents}
								<div className="cell">
									<br />
									<div className="switch tiny grid-x align-middle">
										<input
											type="checkbox"
											className="switch-input"
											name="giftOrder"
											id="giftOrder"
											aria-label="choose this item as gift"
										/>
										<label htmlFor="giftOrder" className="switch-paddle" />
										<label htmlFor="giftOrder" style={{paddingLeft: "5px"}}>
											This order is a gift
										</label>
									</div>
									<p className="subheader" style={{marginTop: "-15px"}}>
										<small>Prices will not be shown on packing slip</small>
									</p>
								</div>
								<div className="cell">
									<label>
										<textarea
											className="margin-0"
											placeholder="Add a note to {ShopName} (Optional)"
											rows={2}
											defaultValue={""}
										/>
										<small className="subheader display-block padding-bottom-1">
											Use the space above to add special instructions or
											personalization details, if applicable.
										</small>
									</label>
								</div>
								<div className="cell">
									<hr />
									<div className="grid-x grid-padding-x medium-up-2 padding-top-1">
										<div className="cell grid-x">
											<fieldset className="cell">
												<legend className="lead">Payment method</legend>
												<div className="switch small grid-x align-middle">
													<input
														className="switch-input"
														type="radio"
														name="payment"
														defaultValue="other"
														id="otherPM"
														aria-label="other payments like visa, mastercard or american express"
													/>
													<label className="switch-paddle" htmlFor="otherPM" />
													<label htmlFor="otherPM">
														<span className="h3" style={{paddingLeft: "10px"}}>
															<i className="fab fa-cc-visa" />
															<i className="fab fa-cc-mastercard" />
															<i className="fab fa-cc-amex" />
														</span>
													</label>
												</div>
												<div className="switch small grid-x align-middle">
													<input
														className="switch-input"
														type="radio"
														name="payment"
														defaultValue="paypal"
														id="paypalPM"
														aria-label="pays with paypal"
														defaultChecked
													/>
													<label htmlFor="paypalPM" className="switch-paddle" />
													<label htmlFor="paypalPM">
														<span className="h3" style={{paddingLeft: "10px"}}>
															<i className="fab fa-cc-paypal" />
														</span>
													</label>
												</div>
											</fieldset>
											<div className="cell align-self-bottom text-center">
												<button type="submit" className="button expanded">
													<strong>Proceed to checkout</strong>
												</button>
												<small className="subheader">
													Local taxes included (where applicable). Additional
													duties and taxes <a href="#">may apply</a>.
												</small>
											</div>
										</div>
										<div className="cell">
											<br className="show-for-small-only" />
											<div className="grid-x small-up-2">
												<p className="cell">Item(s) total</p>
												<p className="cell text-right">USD {priceTotal}</p>
												<p className="cell">Shipping</p>
												<p className="cell text-right">USD 0</p>
												<small className="cell padding-bottom-1">
													(To Vietnam)
												</small>
											</div>
											<hr />
											<div className="grid-x small-up-2 padding-top-1">
												<strong className="cell">
													Total ({itemsTotal} item)
												</strong>
												<strong className="cell text-right">
													USD {priceTotal}
												</strong>
											</div>
										</div>
									</div>
								</div>
							</div>
						</form>
					) : (
						<div className="text-center padding-vertical-1">
							<p className="h4">
								<b>Your cart is empty.</b>
							</p>
							<p>
								<a className="underline" href="#">
									Way to shop new, novel and beautiful products
								</a>
							</p>
						</div>
					)}
					<button
						type="button"
						className="close-button"
						data-close=""
						aria-label="close this modal">
						<span className="lead" aria-hidden="true">
							&times;
						</span>
					</button>
				</div>
			</div>
		);
	}
}
export default CartCheckout;
