import React, {Component} from "react";
import {createFragmentContainer, graphql} from "react-relay";
import DeleteOrderMutation from "./DeleteOrderMutation";
import DeleteOrderProductMutation from "../OrderProduct/DeleteOrderProductMutation";

interface IOrderProps {
	viewer: {
		id: string;
	};
	order: {
		id: string;
		customerName: string;
		customerStreetAddress: string;
		customerCity: string;
		customerPostcode: number;
		customerState: string;
		customerCountry: string;
		customerPhone: string;
		customerEmailAddress: string;
		deliveryName: string;
		deliveryStreetAddress: string;
		deliveryCity: string;
		deliveryPostcode: number;
		deliveryState: string;
		deliveryCountry: string;
		paymentMethod: string;
		ccType: string;
		ccOwner: string;
		ccNumber: number;
		ccExpires: string;
		shippingCost: number;
		shippingMethod: string;
		orderFinishedDate: string;
		comments: string;
		currency: string;
		currencyValue: number;
		createdAt: string;
		updatedAt: string;
		customer: {
			customerFirstname: string;
			customerLastname: string;
		};
		orderProduct: {
			edges: [
				{
					node: {
						id: string;
						product: {
							productDescription: {
								productName: string;
							};
						};
					};
				}
			];
		};
	};
}
const Order = (props: IOrderProps) => {
	const deleteOrderHandler = () => {
		props.order.orderProduct.edges.forEach(({node}) => {
			DeleteOrderProductMutation(node.id, props.viewer.id);
		});
		DeleteOrderMutation(props.order.id, props.viewer.id);
	};

	return (
		<div className="grid-x shadow padding-1">
			<div className="cell text-right">
				<button
					type="button"
					className="button hollow"
					onClick={deleteOrderHandler}>
					Delete
				</button>
			</div>
			<div className="cell grid-x">
				id:
				<small className="cell margin-bottom-1">
					<b>{props.order.id}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				customerName:
				<small className="cell margin-bottom-1">
					<b>{props.order.customerName}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				customerStreetAddress:
				<small className="cell margin-bottom-1">
					<b>{props.order.customerStreetAddress}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				customerCity:
				<small className="cell margin-bottom-1">
					<b>{props.order.customerCity}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				customerPostcode:
				<small className="cell margin-bottom-1">
					<b>{props.order.customerPostcode}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				customerState:
				<small className="cell margin-bottom-1">
					<b>{props.order.customerState}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				customerCountry:
				<small className="cell margin-bottom-1">
					<b>{props.order.customerCountry}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				customerPhone:
				<small className="cell margin-bottom-1">
					<b>{props.order.customerPhone}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				customerEmailAddress:
				<small className="cell margin-bottom-1">
					<b>{props.order.customerEmailAddress}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				deliveryName:
				<small className="cell margin-bottom-1">
					<b>{props.order.deliveryName}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				deliveryStreetAddress:
				<small className="cell margin-bottom-1">
					<b>{props.order.deliveryStreetAddress}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				deliveryCity:
				<small className="cell margin-bottom-1">
					<b>{props.order.deliveryCity}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				deliveryPostcode:
				<small className="cell margin-bottom-1">
					<b>{props.order.deliveryPostcode}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				deliveryState:
				<small className="cell margin-bottom-1">
					<b>{props.order.deliveryState}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				deliveryCountry:
				<small className="cell margin-bottom-1">
					<b>{props.order.deliveryCountry}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				paymentMethod:
				<small className="cell margin-bottom-1">
					<b>{props.order.paymentMethod}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				ccType:
				<small className="cell margin-bottom-1">
					<b>{props.order.ccType}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				ccOwner:
				<small className="cell margin-bottom-1">
					<b>{props.order.ccOwner}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				ccNumber:
				<small className="cell margin-bottom-1">
					<b>{props.order.ccNumber}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				ccExpires:
				<small className="cell margin-bottom-1">
					<b>{props.order.ccExpires}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				shippingCost:
				<small className="cell margin-bottom-1">
					<b>{props.order.shippingCost}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				shippingMethod:
				<small className="cell margin-bottom-1">
					<b>{props.order.shippingMethod}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				orderFinishedDate:
				<small className="cell margin-bottom-1">
					<b>{props.order.orderFinishedDate}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				comments:
				<small className="cell margin-bottom-1">
					<b>{props.order.comments}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				currency:
				<small className="cell margin-bottom-1">
					<b>{props.order.currency}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				currencyValue:
				<small className="cell margin-bottom-1">
					<b>{props.order.currencyValue}</b>
				</small>
			</div>{" "}
			<div className="cell grid-x">
				[Belong to] customerLastname, customerFirstname:
				<small className="cell margin-bottom-1">
					<b>
						{props.order.customer.customerLastname +
							", " +
							props.order.customer.customerFirstname}
					</b>
				</small>
			</div>
			{props.order.orderProduct.edges.length > 0 && (
				<div className="cell grid-x">
					[Belong to] orderProduct-product-productDescription-productName:
					{props.order.orderProduct.edges.map(({node}, idx) => {
						return (
							<small key={idx} className="cell margin-bottom-1">
								{node.product.productDescription.productName}
							</small>
						);
					})}
				</div>
			)}
		</div>
	);
};

export default createFragmentContainer(Order, {
	order: graphql`
		fragment Order_order on Order {
			id
			customerName
			customerStreetAddress
			customerCity
			customerPostcode
			customerState
			customerCountry
			customerPhone
			customerEmailAddress
			deliveryName
			deliveryStreetAddress
			deliveryCity
			deliveryPostcode
			deliveryState
			deliveryCountry
			paymentMethod
			ccType
			ccOwner
			ccNumber
			ccExpires
			shippingCost
			shippingMethod
			orderFinishedDate
			comments
			currency
			currencyValue
			createdAt
			updatedAt
			customer {
				customerFirstname
				customerLastname
			}
			orderProduct {
				edges {
					node {
						id
						product {
							productDescription {
								productName
							}
						}
					}
				}
			}
		}
	`,
	viewer: graphql`
		fragment Order_viewer on Viewer {
			id
		}
	`
});
