import React from "react";
import {Link} from "react-router-dom";
import {createFragmentContainer, graphql} from "react-relay";

interface IReviewProps {
	review: {
		createdAt: string;
		customer: {
			customerFirstname: string;
			customerLastname: string;
			customerImage: string;
		};
		reviewDescription: {
			reviewText: string;
		};
	};
	idx: number;
}
const Review = (props: IReviewProps) => {
	return (
		<div className="grid-x">
			<div className="cell medium-2 margin-vertical-1">
				<Link to="/user/test">
					<img
						className="rounded img-with-shadow"
						src={props.review.customer.customerImage}
						alt="Avatar 1"
					/>
				</Link>
			</div>
			<div className="cell medium-10 padding-left-1">
				<small className="p">
					<Link to="/user/test" className="underline" href="#">
						{props.review.customer.customerLastname},{" "}
						{props.review.customer.customerFirstname}
					</Link>
					{props.review.createdAt}
				</small>
				<small>
					<i className="fas fa-star" />
					<i className="fas fa-star" />
					<i className="fas fa-star" />
					<i className="fas fa-star" />
					<i className="fas fa-star" />
				</small>
				<div className="truncate-wrapper">
					<p
						className="text-truncate normal"
						id={"review" + props.idx}
						data-toggler=".text-truncate">
						<small>{props.review.reviewDescription.reviewText}</small>
					</p>
					<div>
						<p>
							<a
								className="underline padding-left-1 review-toggler"
								data-toggle={"review" + props.idx}>
								Read more +
							</a>
						</p>
					</div>
				</div>
			</div>
		</div>
	);
};

export default createFragmentContainer(Review, {
	review: graphql`
		fragment Review_review on Review {
			createdAt
			customer {
				customerFirstname
				customerLastname
				customerImage
			}
			reviewDescription {
				reviewText
			}
		}
	`
});
