import React from "react";
import uuid from "uuid";
import ProductInCart from "./ProductInCart/ProductInCart";

interface IShopInCartProps {
	item: {
		shopName: string;
		products: {
			id: string;
			productImage: string;
			productPrice: number;
			productQuantity: number;
			productDescription: {
				productName: string;
			};
		}[];
	};
	total: {
		[x: string]: {
			itemsTotal: number;
			priceTotal: number;
			priceAverage: number;
		};
	}[];
	idx: number;
	removeItemHandler: (id: string) => void;
	saveOnLocal: (id: string, quantity: number) => void;
}
const ShopInCart = (props: IShopInCartProps) => {
	return (
		<div className="cell grid-x">
			<div className="cell medium-8">
				<div className="cell grid-x">
					<div className="cell grid-x">
						<p className="cell">
							<i className="fas fa-store" /> {props.item.shopName}
						</p>
						{props.item.products.map((product) => {
							return (
								<ProductInCart
									key={uuid.v4()}
									product={product}
									onRemoveClick={props.removeItemHandler}
									onExitSave={props.saveOnLocal}
								/>
							);
						})}
					</div>
				</div>
			</div>
			<div className="cell medium-4 text-right">
				<br className="show-for-small-only" />
				<a href="#">Contact Shop</a>
				<small className="display-block">
					<strong>
						USD {props.total[props.idx][props.item.shopName].priceTotal}
					</strong>
				</small>
				<small>
					(USD {props.total[props.idx][props.item.shopName].priceAverage} each)
				</small>
				<hr className="margin-1" />
				<small>Shipping fee: USD 0</small>
				<small className="display-block subheader text-right">
					Ready to ship in 1-3 business days from the United States
				</small>
			</div>
		</div>
	);
};

export default ShopInCart;
