import React from "react";
import {Link} from "react-router-dom";
import {createFragmentContainer, graphql} from "react-relay";

interface ICategoryItemProps {
	product: {
		id: string;
		productImage: string;
		productPrice: number;
		productDescription: {
			productName: string;
		};
		shop: {
			shopName: string;
		};
	};
}
const CategoryItem = (props: ICategoryItemProps) => {
	return (
		<Link
			to={"/product/" + props.product.id}
			className="cell padding-top-1"
			title={props.product.productDescription.productName}>
			<div>
				<img
					className="thumbnail img-with-shadow"
					src={props.product.productImage}
					alt="Category product 1"
				/>
			</div>
			<div className="truncate-wrapper">
				<p className="text-truncate margin-0">
					{props.product.productDescription.productName}
				</p>
			</div>
			<small className="subheader">{props.product.shop.shopName}</small>
			<small className="p">
				<i className="fas fa-star" />
				<i className="fas fa-star" />
				<i className="fas fa-star" />
				<i className="fas fa-star" />
				<i className="fas fa-star" />
				(1,020)
			</small>
			<span className="p padding-bottom-1">
				<b>USD {props.product.productPrice}</b>
			</span>
			<p>
				<small className="medal">
					<i className="fas fa-medal" /> Bestseller
				</small>
			</p>
		</Link>
	);
};

export default createFragmentContainer(CategoryItem, {
	product: graphql`
		fragment CategoryItem_product on Product {
			id
			productImage
			productPrice
			productDescription {
				productName
			}
			shop {
				shopName
			}
		}
	`
});
