import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import uuid from "uuid";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation CreateShopMutation($input: CreateShopInput!) {
		createShop(input: $input) {
			shop {
				id
				shopName
				shopImage
				shopInfo {
					shopAddress
					shopUrl
				}
			}
		}
	}
`;

function CreateShopMutation(
	shopObj: {
		shopName: string;
		shopImage: string;
		shopAddress: string;
		shopUrl: string;
		languageId: string;
	},
	viewerId: string,
	cb: () => void
) {
	const variables = {
		input: {
			shopName: shopObj.shopName,
			shopImage: shopObj.shopImage,
			shopInfo: {
				shopAddress: shopObj.shopAddress,
				shopUrl: shopObj.shopUrl,
				languageId: shopObj.languageId
			},
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			// Create new temporary shop.
			const id = uuid.v4();
			const newShop = oldStore.create(id, "Shop");
			newShop.setValue(id, "id");
			newShop.setValue(shopObj.shopName, "shopName");
			newShop.setValue(shopObj.shopImage, "shopImage");
			newShop.setValue(shopObj.shopAddress, "shopAddress");
			newShop.setValue(shopObj.shopUrl, "shopUrl");
			// Add the temp shop to the oldStore.
			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"ShopList_allShops"
			);
			if (connection) {
				ConnectionHandler.insertEdgeAfter(connection, newShop);
			}
		},
		updater: (newStore) => {
			// Get the newly created shop.
			const createShopField = newStore.getRootField("createShop");
			if (createShopField) {
				const newShop = createShopField.getLinkedRecord("shop");
				// Set the new shop to the shoplist.
				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"ShopList_allShops"
				);
				if (connection) {
					ConnectionHandler.insertEdgeAfter(connection, newShop);
				}
			} else {
				console.log("Connection is null/undefined...");
			}
		},
		onCompleted: (variables, error) => {
			cb();
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default CreateShopMutation;
