import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation DeleteOrderMutation($input: DeleteOrderInput!) {
		deleteOrder(input: $input) {
			deletedId
		}
	}
`;

function DeleteOrderMutation(orderId: string, viewerId: string) {
	const variables = {
		input: {
			id: orderId,
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"OrderList_allOrders"
			);
			if (connection) {
				ConnectionHandler.deleteNode(connection, orderId);
			}
		},
		updater: (newStore) => {
			const deleteOrderField = newStore.getRootField("deleteOrder");
			if (deleteOrderField) {
				const deletedId = deleteOrderField.getValue("deletedId");

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"OrderList_allOrders"
				);
				if (connection) {
					ConnectionHandler.deleteNode(connection, deletedId);
				}
			}
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default DeleteOrderMutation;
