import React, {Component} from "react";
import CustomerList from "./CustomerList";
import {QueryRenderer, graphql} from "react-relay";
import environment from "../../../../schema/environment";

const CustomerAppAllCustomersQuery = graphql`
	query CustomerAppAllCustomersQuery {
		viewer {
			...CustomerList_viewer
		}
	}
`;

const CustomerApp = () => {
	return (
		<QueryRenderer
			environment={environment}
			query={CustomerAppAllCustomersQuery}
			variables={{}}
			render={({error, props}) => {
				if (error) {
					return (
						<p>
							<b>Error: {error.message}</b>
						</p>
					);
				} else if (!props) {
					return (
						<p>
							<b>Loading...</b>
						</p>
					);
				} else {
					return <CustomerList viewer={props.viewer} />;
				}
			}}
		/>
	);
};

export default CustomerApp;
