import React from "react";
import {createFragmentContainer, graphql} from "react-relay";
import {Link} from "react-router-dom";
import OrderProduct from "./OrderProduct";

interface IOrderProductListProps {
	viewer: {
		id: string;
		allOrderProducts: {
			edges: [
				{
					node: {
						id: string;
						order: {
							customerName: string;
							customer: {
								customerFirstname: string;
								customerLastname: string;
							};
						};
						product: {
							productDescription: {
								productName: string;
							};
						};
					};
				}
			];
		};
	};
}
const OrderProductList = (props: IOrderProductListProps) => {
	return (
		<div className="grid-x align-center padding-1">
			<div className="grid-x" style={{maxWidth: 1000}}>
				<div className="cell text-center">
					<Link
						to="/create/createorderproduct"
						className="button margin-bottom-1">
						Create new OrderProduct
					</Link>
				</div>
				<div className="cell grid-x small-up-2 medium-up-3">
					{props.viewer.allOrderProducts.edges.map(({node}, idx) => {
						return (
							<div className="cell" key={idx}>
								<OrderProduct orderProduct={node} viewer={props.viewer} />
							</div>
						);
					})}
				</div>
			</div>
		</div>
	);
};

export default createFragmentContainer(OrderProductList, {
	viewer: graphql`
		fragment OrderProductList_viewer on Viewer {
			...OrderProduct_viewer
			allOrderProducts(first: 10)
				@connection(key: "OrderProductList_allOrderProducts", filters: []) {
				edges {
					node {
						...OrderProduct_orderProduct
					}
				}
			}
		}
	`
});
