import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import uuid from "uuid";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation CreateCustomerMutation($input: CreateCustomerInput!) {
		createCustomer(input: $input) {
			customer {
				id
				customerFirstname
				customerLastname
				customerImage
				customerGender
				customerDob
				customerPhone
				customerEmailAddress
				customerPassword
				customerNewsletter
			}
		}
	}
`;

function CreateCustomerMutation(
	customerObj: {
		customerFirstname: string;
		customerLastname: string;
		customerImage: string;
		customerGender: boolean;
		customerDob: string;
		customerPhone: string;
		customerEmailAddress: string;
		customerPassword: string;
		customerNewsletter: boolean;
	},
	viewerId: string,
	cb: () => void
) {
	const variables = {
		input: {
			customerFirstname: customerObj.customerFirstname,
			customerLastname: customerObj.customerLastname,
			customerImage: customerObj.customerImage,
			customerGender: customerObj.customerGender,
			customerDob: customerObj.customerDob,
			customerPhone: customerObj.customerPhone,
			customerEmailAddress: customerObj.customerEmailAddress,
			customerPassword: customerObj.customerPassword,
			customerNewsletter: customerObj.customerNewsletter,
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const id = uuid.v4();
			const newCustomer = oldStore.create(id, "Customer");
			newCustomer.setValue(id, "id");
			newCustomer.setValue(customerObj.customerFirstname, "customerFirstname");
			newCustomer.setValue(customerObj.customerLastname, "customerLastname");
			newCustomer.setValue(customerObj.customerImage, "customerImage");
			newCustomer.setValue(customerObj.customerGender, "customerGender");
			newCustomer.setValue(customerObj.customerDob, "customerDob");
			newCustomer.setValue(customerObj.customerPhone, "customerPhone");
			newCustomer.setValue(
				customerObj.customerEmailAddress,
				"customerEmailAddress"
			);
			newCustomer.setValue(customerObj.customerPassword, "customerPassword");
			newCustomer.setValue(
				customerObj.customerNewsletter,
				"customerNewsletter"
			);

			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"CustomerList_allCustomers"
			);
			if (connection) {
				ConnectionHandler.insertEdgeAfter(connection, newCustomer);
			}
		},
		updater: (newStore) => {
			const createCustomerField = newStore.getRootField("createCustomer");
			if (createCustomerField) {
				const newCustomer = createCustomerField.getLinkedRecord("customer");

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"CustomerList_allCustomers"
				);
				if (connection) {
					ConnectionHandler.insertEdgeAfter(connection, newCustomer);
				}
			}
		},
		onCompleted: () => {
			cb();
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default CreateCustomerMutation;
