import React, {Component} from "react";
import {createFragmentContainer, graphql} from "react-relay";
import {Link} from "react-router-dom";
import Shop from "./Shop";

interface IShopListProps {
	viewer: {
		id: string;
		allShops: {
			edges: [
				{
					node: {
						id: string;
						shopName: string;
						shopImage: string;
						shopInfo: {
							id: string;
							shopAddress: string;
							shopUrl: string;
						};
					};
				}
			];
		};
	};
}
const ShopList = (props: IShopListProps) => {
	return (
		<div
			className="grid-x shadow align-center padding-1"
			style={{maxWidth: 400}}>
			<Link to="/create/createshop" className="button margin-bottom-1">
				Create new Shop
			</Link>
			{props.viewer.allShops.edges.map(({node}, idx) => {
				return <Shop key={idx} shop={node} viewer={props.viewer} />;
			})}
		</div>
	);
};

export default createFragmentContainer(ShopList, {
	viewer: graphql`
		fragment ShopList_viewer on Viewer {
			...Shop_viewer
			allShops(first: 10, orderBy: shopName_ASC)
				@connection(key: "ShopList_allShops", filters: []) {
				edges {
					node {
						...Shop_shop
					}
				}
			}
		}
	`
});
