import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation DeleteOrderProductMutation($input: DeleteOrderProductInput!) {
		deleteOrderProduct(input: $input) {
			deletedId
		}
	}
`;

function DeleteOrderProductMutation(orderProductId: string, viewerId: string) {
	const variables = {
		input: {
			id: orderProductId,
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"OrderProductList_allOrderProducts"
			);
			if (connection) {
				ConnectionHandler.deleteNode(connection, orderProductId);
			}
		},
		updater: (newStore) => {
			const deleteOrderProductField = newStore.getRootField(
				"deleteOrderProduct"
			);
			if (deleteOrderProductField) {
				const deletedId = deleteOrderProductField.getValue("deletedId");

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"OrderProductList_allOrderProducts"
				);
				if (connection) {
					ConnectionHandler.deleteNode(connection, deletedId);
				}
			}
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default DeleteOrderProductMutation;
