import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import uuid from "uuid";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation CreateOrderProductMutation($input: CreateOrderProductInput!) {
		createOrderProduct(input: $input) {
			orderProduct {
				order {
					customerName
				}
				product {
					productDescription {
						productName
					}
				}
			}
		}
	}
`;

function CreateOrderProductMutation(
	orderProductObj: {
		orderId: string;
		customerName: string;
		productId: string;
		productName: string;
	},
	viewerId: string,
	cb: (() => void) | null
) {
	const variables = {
		input: {
			orderId: orderProductObj.orderId,
			productId: orderProductObj.productId,
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const id = uuid.v4();
			const newOrderProduct = oldStore.create(id, "OrderProduct");
			newOrderProduct.setValue(orderProductObj.orderId, "orderId");
			newOrderProduct.setValue(orderProductObj.customerName, "customerName");
			newOrderProduct.setValue(orderProductObj.productId, "productId");
			newOrderProduct.setValue(orderProductObj.productName, "productName");

			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"OrderProductList_allOrderProducs"
			);
			if (connection) {
				ConnectionHandler.insertEdgeAfter(connection, newOrderProduct);
			}
		},
		updater: (newStore) => {
			const createOrderProductField = newStore.getRootField(
				"createOrderProduct"
			);
			if (createOrderProductField) {
				const newOrderProduct = createOrderProductField.getLinkedRecord(
					"orderProduct"
				);

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"OrderProductList_allOrderProducs"
				);

				if (connection) {
					ConnectionHandler.insertEdgeAfter(connection, newOrderProduct);
				}
			}
		},
		onCompleted: (variables, error) => {
			if (cb) {
				cb();
			}
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default CreateOrderProductMutation;
