import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation DeleteCustomerMutation($input: DeleteCustomerInput!) {
		deleteCustomer(input: $input) {
			deletedId
		}
	}
`;

function DeteleteCustomerMutation(customerId: string, viewerId: string) {
	const variables = {
		input: {
			id: customerId,
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"CustomerList_allCustomers"
			);
			if (connection) {
				ConnectionHandler.deleteNode(connection, customerId);
			}
		},
		updater: (newStore) => {
			const deleteCustomerField = newStore.getRootField("deleteCustomer");
			if (deleteCustomerField) {
				const deletedId = deleteCustomerField.getValue("deletedId");

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"CustomerList_allCustomers"
				);
				if (connection) {
					ConnectionHandler.deleteNode(connection, deletedId);
				}
			}
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default DeteleteCustomerMutation;
