import React, {Component} from "react";
import {QueryRenderer, graphql} from "react-relay";
import environment from "../../../../schema/environment";
import OrderProductList from "./OrderProductList";

const OrderProductAppAllOrderProductsQuery = graphql`
	query OrderProductAppAllOrderProductsQuery {
		viewer {
			...OrderProductList_viewer
		}
	}
`;

const OrderProductApp = () => {
	return (
		<QueryRenderer
			environment={environment}
			query={OrderProductAppAllOrderProductsQuery}
			variables={{}}
			render={({error, props}) => {
				if (error) {
					return (
						<p>
							<b>Error: {error.message}</b>
						</p>
					);
				} else if (!props) {
					return (
						<p>
							<b>Loading...</b>
						</p>
					);
				} else {
					return <OrderProductList viewer={props.viewer} />;
				}
			}}
		/>
	);
};

export default OrderProductApp;
