import React, {Component} from "react";
import {QueryRenderer, graphql} from "react-relay";
import {withRouter, RouteComponentProps} from "react-router-dom";
import environment from "../../../../schema/environment";
import CreateOrderMutation from "./CreateOrderMutation";

interface IOrderCreateState {
	customerName: string;
	customerStreetAddress: string;
	customerCity: string;
	customerPostcode: number;
	customerState: string;
	customerCountry: string;
	customerPhone: string;
	customerEmailAddress: string;
	deliveryName: string;
	deliveryStreetAddress: string;
	deliveryCity: string;
	deliveryPostcode: number;
	deliveryState: string;
	deliveryCountry: string;
	paymentMethod: string;
	ccType: string;
	ccOwner: string;
	ccNumber: number;
	ccExpires: string;
	shippingCost: number;
	shippingMethod: string;
	orderFinishedDate: string;
	comments: string;
	currency: string;
	currencyValue: number;
}
interface IUpdatedState extends IOrderCreateState {
	customerId: string;
	customerFirstname: string;
	customerLastname: string;
}
class OrderCreate extends Component {
	props: RouteComponentProps;
	state: IOrderCreateState;

	constructor(props: RouteComponentProps) {
		super(props);
		this.props = props;
		this.state = {
			customerName: "John E. Doe",
			customerStreetAddress: "10672 Kenai Spur Highway",
			customerCity: "Kenai",
			customerPostcode: 99611,
			customerState: "Arkansas",
			customerCountry: "USA",
			customerPhone: "9072834402",
			customerEmailAddress: "john.doe@example.com",
			deliveryName: "Joy Groaning",
			deliveryStreetAddress: "2780 West Chandler Blvd",
			deliveryCity: "Chandler",
			deliveryPostcode: 85224,
			deliveryState: "Arizona",
			deliveryCountry: "USA",
			paymentMethod: "paypal",
			ccType: "",
			ccOwner: "",
			ccNumber: 0,
			ccExpires: "2020-12-12",
			shippingCost: 100,
			shippingMethod: "Fixed",
			orderFinishedDate: "2020-12-12",
			comments: "Tis is gud",
			currency: "USD",
			currencyValue: 1
		};
	}

	inputHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
		this.setState({
			[e.target.name]: e.target.value
		});
	};

	createOrderHandler = (viewerId: string) => {
		const updatedState = Object.assign({}, this.state) as IUpdatedState;
		const customerSelect = document.querySelector(
			"#customerSelect"
		) as HTMLSelectElement;
		const productSelects = document.querySelectorAll(
			'[id^="productSelect"]'
		) as NodeListOf<HTMLSelectElement>;
		if (customerSelect) {
			const selectedCustomer =
				customerSelect.options[customerSelect.selectedIndex];

			updatedState.customerId = selectedCustomer.value;
			[
				updatedState.customerLastname,
				updatedState.customerFirstname
			] = selectedCustomer.text.split(",");

			CreateOrderMutation(updatedState, viewerId, () => {
				this.props.history.push("/create/listorder");
			});
		}
	};

	render() {
		const OrderCreateViewerQuery = graphql`
			query OrderCreateViewerQuery {
				viewer {
					id
					allCustomers(first: 10, orderBy: customerLastname_ASC) {
						edges {
							node {
								id
								customerFirstname
								customerLastname
							}
						}
					}
				}
			}
		`;

		return (
			<QueryRenderer
				environment={environment}
				query={OrderCreateViewerQuery}
				variables={{}}
				render={({error, props}) => {
					if (error) {
						return (
							<p>
								<b>Error: {error}</b>
							</p>
						);
					} else if (!props) {
						return (
							<p>
								<b>Loading...</b>
							</p>
						);
					} else {
						return (
							<div className="grid-x align-center margin-vertical-1">
								<div
									className="grid-x shadow padding-1"
									style={{maxWidth: 300}}>
									<div className="cell grid-x">
										customerName:
										<input
											required
											type="text"
											name="customerName"
											className="cell margin-bottom-1"
											value={this.state.customerName}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerStreetAddress:
										<input
											required
											type="text"
											name="customerStreetAddress"
											className="cell margin-bottom-1"
											value={this.state.customerStreetAddress}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerCity:
										<input
											required
											type="text"
											name="customerCity"
											className="cell margin-bottom-1"
											value={this.state.customerCity}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerPostcode:
										<input
											required
											type="text"
											name="customerPostcode"
											className="cell margin-bottom-1"
											value={this.state.customerPostcode}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerState:
										<input
											required
											type="text"
											name="customerState"
											className="cell margin-bottom-1"
											value={this.state.customerState}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerCountry:
										<input
											required
											type="text"
											name="customerCountry"
											className="cell margin-bottom-1"
											value={this.state.customerCountry}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerPhone:
										<input
											required
											type="text"
											name="customerPhone"
											className="cell margin-bottom-1"
											value={this.state.customerPhone}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										customerEmailAddress:
										<input
											required
											type="text"
											name="customerEmailAddress"
											className="cell margin-bottom-1"
											value={this.state.customerEmailAddress}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										deliveryName:
										<input
											required
											type="text"
											name="deliveryName"
											className="cell margin-bottom-1"
											value={this.state.deliveryName}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										deliveryStreetAddress:
										<input
											required
											type="text"
											name="deliveryStreetAddress"
											className="cell margin-bottom-1"
											value={this.state.deliveryStreetAddress}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										deliveryCity:
										<input
											required
											type="text"
											name="deliveryCity"
											className="cell margin-bottom-1"
											value={this.state.deliveryCity}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										deliveryPostcode:
										<input
											required
											type="text"
											name="deliveryPostcode"
											className="cell margin-bottom-1"
											value={this.state.deliveryPostcode}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										deliveryState:
										<input
											required
											type="text"
											name="deliveryState"
											className="cell margin-bottom-1"
											value={this.state.deliveryState}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										deliveryCountry:
										<input
											required
											type="text"
											name="deliveryCountry"
											className="cell margin-bottom-1"
											value={this.state.deliveryCountry}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										paymentMethod:
										<input
											required
											type="text"
											name="paymentMethod"
											className="cell margin-bottom-1"
											value={this.state.paymentMethod}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										ccType:
										<input
											required
											type="text"
											name="ccType"
											className="cell margin-bottom-1"
											value={this.state.ccType}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										ccOwner:
										<input
											required
											type="text"
											name="ccOwner"
											className="cell margin-bottom-1"
											value={this.state.ccOwner}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										ccNumber:
										<input
											required
											type="text"
											name="ccNumber"
											className="cell margin-bottom-1"
											value={this.state.ccNumber}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										ccExpires:
										<input
											required
											type="text"
											name="ccExpires"
											className="cell margin-bottom-1"
											value={this.state.ccExpires}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										shippingCost:
										<input
											required
											type="text"
											name="shippingCost"
											className="cell margin-bottom-1"
											value={this.state.shippingCost}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										shippingMethod:
										<input
											required
											type="text"
											name="shippingMethod"
											className="cell margin-bottom-1"
											value={this.state.shippingMethod}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										orderFinishedDate:
										<input
											required
											type="text"
											name="orderFinishedDate"
											className="cell margin-bottom-1"
											value={this.state.orderFinishedDate}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										comments:
										<input
											required
											type="text"
											name="comments"
											className="cell margin-bottom-1"
											value={this.state.comments}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										currency:
										<input
											required
											type="text"
											name="currency"
											className="cell margin-bottom-1"
											value={this.state.currency}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										currencyValue:
										<input
											required
											type="text"
											name="currencyValue"
											className="cell margin-bottom-1"
											value={this.state.currencyValue}
											onChange={this.inputHandler}
										/>
									</div>
									<div className="cell grid-x">
										[Belong to] customerFirstname customerLastname:
										<select id="customerSelect">
											{props.viewer.allCustomers.edges.map(
												(
													{
														node
													}: {
														node: {
															id: string;
															customerFirstname: string;
															customerLastname: string;
														};
													},
													idx: number
												) => {
													return (
														<option key={idx} value={node.id}>
															{node.customerLastname}, {node.customerFirstname}
														</option>
													);
												}
											)}
										</select>
									</div>
									<div className="cell text-right">
										<button
											type="button"
											className="button"
											onClick={(e) => {
												this.createOrderHandler(props.viewer.id);
											}}>
											Submit
										</button>
									</div>
								</div>
							</div>
						);
					}
				}}
			/>
		);
	}
}

export default withRouter(OrderCreate);
