import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation DeleteProductMutation(
		$input1: DeleteProductDescriptionInput!
		$input2: DeleteProductInput!
	) {
		deleteProductDescription(input: $input1) {
			deletedId
		}
		deleteProduct(input: $input2) {
			deletedId
		}
	}
`;

function DeleteProductMutation(
	productDescriptionId: string,
	productId: string,
	viewerId: string
) {
	const variables = {
		input1: {
			id: productDescriptionId,
			clientMutationId: ""
		},
		input2: {
			id: productId,
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"ProductList_allProducts"
			);
			if (connection) {
				ConnectionHandler.deleteNode(connection, productId);
			}
		},
		updater: (newStore) => {
			const deleteProductField = newStore.getRootField("deleteProduct");
			if (deleteProductField) {
				const deletedId = deleteProductField.getValue("deletedId");

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"ProductList_allProducts"
				);
				if (connection) {
					ConnectionHandler.deleteNode(connection, deletedId);
				}
			}
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default DeleteProductMutation;
