import React from "react";
import ReactDOM from "react-dom";
import {
	Router,
	Route,
	Switch,
	withRouter,
	RouteComponentProps
} from "react-router-dom";
import MainApp from "./vsy_src/MainApp";
import ShopApp from "./vsy_src/Shop/ShopApp";
import ShopCreate from "./vsy_src/Shop/ShopCreate";
import CustomerApp from "./vsy_src/Customer/CustomerApp";
import CustomerCreate from "./vsy_src/Customer/CustomerCreate";
import ProductApp from "./vsy_src/Product/ProductApp";
import ProductCreate from "./vsy_src/Product/ProductCreate";
import OrderApp from "./vsy_src/Order/OrderApp";
import OrderCreate from "./vsy_src/Order/OrderCreate";
import OrderProductApp from "./vsy_src/OrderProduct/OrderProductApp";
import OrderProductCreate from "./vsy_src/OrderProduct/OrderProductCreate";

const CreatePage = (props: RouteComponentProps) => {
	return (
		<div>
			<Router history={props.history}>
				<Switch>
					<Route path="/create/main" component={MainApp} />
					<Route path="/create/listshop" component={ShopApp} />
					<Route path="/create/createshop" component={ShopCreate} />
					<Route path="/create/listcustomer" component={CustomerApp} />
					<Route path="/create/createcustomer" component={CustomerCreate} />
					<Route path="/create/listproduct" component={ProductApp} />
					<Route path="/create/createproduct" component={ProductCreate} />
					<Route path="/create/listorder" component={OrderApp} />
					<Route path="/create/createorder" component={OrderCreate} />
					<Route path="/create/listorderproduct" component={OrderProductApp} />
					<Route
						path="/create/createorderproduct"
						component={OrderProductCreate}
					/>
				</Switch>
			</Router>
		</div>
	);
};

export default withRouter(CreatePage);
