import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import uuid from "uuid";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation CreateOrderMutation($input: CreateOrderInput!) {
		createOrder(input: $input) {
			order {
				id
				customerName
				customerStreetAddress
				customerCity
				customerPostcode
				customerState
				customerCountry
				customerPhone
				customerEmailAddress
				deliveryName
				deliveryStreetAddress
				deliveryCity
				deliveryPostcode
				deliveryState
				deliveryCountry
				paymentMethod
				ccType
				ccOwner
				ccNumber
				ccExpires
				shippingCost
				shippingMethod
				orderFinishedDate
				comments
				currency
				currencyValue
				createdAt
				updatedAt
				customer {
					customerFirstname
					customerLastname
				}
				orderProduct {
					edges {
						node {
							product {
								productDescription {
									productName
								}
							}
						}
					}
				}
			}
		}
	}
`;

function CreateOrderMutation(
	orderObj: {
		customerName: string;
		customerStreetAddress: string;
		customerCity: string;
		customerPostcode: number;
		customerState: string;
		customerCountry: string;
		customerPhone: string;
		customerEmailAddress: string;
		deliveryName: string;
		deliveryStreetAddress: string;
		deliveryCity: string;
		deliveryPostcode: number;
		deliveryState: string;
		deliveryCountry: string;
		paymentMethod: string;
		ccType: string;
		ccOwner: string;
		ccNumber: number;
		ccExpires: string;
		shippingCost: number;
		shippingMethod: string;
		orderFinishedDate: string;
		comments: string;
		currency: string;
		currencyValue: number;
		customerId: string;
		customerFirstname: string;
		customerLastname: string;
	},
	viewerId: string,
	cb: () => void
) {
	const variables = {
		input: {
			customerName: orderObj.customerName,
			customerStreetAddress: orderObj.customerStreetAddress,
			customerCity: orderObj.customerCity,
			customerPostcode: orderObj.customerPostcode,
			customerState: orderObj.customerState,
			customerCountry: orderObj.customerCountry,
			customerPhone: orderObj.customerPhone,
			customerEmailAddress: orderObj.customerEmailAddress,
			deliveryName: orderObj.deliveryName,
			deliveryStreetAddress: orderObj.deliveryStreetAddress,
			deliveryCity: orderObj.deliveryCity,
			deliveryPostcode: orderObj.deliveryPostcode,
			deliveryState: orderObj.deliveryState,
			deliveryCountry: orderObj.deliveryCountry,
			paymentMethod: orderObj.paymentMethod,
			ccType: orderObj.ccType,
			ccOwner: orderObj.ccOwner,
			ccNumber: orderObj.ccNumber,
			ccExpires: orderObj.ccExpires,
			shippingCost: orderObj.shippingCost,
			shippingMethod: orderObj.shippingMethod,
			orderFinishedDate: orderObj.orderFinishedDate,
			comments: orderObj.comments,
			currency: orderObj.currency,
			currencyValue: orderObj.currencyValue,
			customerId: orderObj.customerId,
			clientMutationId: ""
		}
	};

	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const id = uuid.v4();
			const newOrder = oldStore.create(id, "Order");
			newOrder.setValue(id, "id");
			newOrder.setValue(orderObj.customerName, "customerName");
			newOrder.setValue(
				orderObj.customerStreetAddress,
				"customerStreetAddress"
			);
			newOrder.setValue(orderObj.customerCity, "customerCity");
			newOrder.setValue(orderObj.customerPostcode, "customerPostcode");
			newOrder.setValue(orderObj.customerState, "customerState");
			newOrder.setValue(orderObj.customerCountry, "customerCountry");
			newOrder.setValue(orderObj.customerPhone, "customerPhone");
			newOrder.setValue(orderObj.customerEmailAddress, "customerEmailAddress");
			newOrder.setValue(orderObj.deliveryName, "deliveryName");
			newOrder.setValue(
				orderObj.deliveryStreetAddress,
				"deliveryStreetAddress"
			);
			newOrder.setValue(orderObj.deliveryCity, "deliveryCity");
			newOrder.setValue(orderObj.deliveryPostcode, "deliveryPostcode");
			newOrder.setValue(orderObj.deliveryState, "deliveryState");
			newOrder.setValue(orderObj.deliveryCountry, "deliveryCountry");
			newOrder.setValue(orderObj.paymentMethod, "paymentMethod");
			newOrder.setValue(orderObj.ccType, "ccType");
			newOrder.setValue(orderObj.ccOwner, "ccOwner");
			newOrder.setValue(orderObj.ccNumber, "ccNumber");
			newOrder.setValue(orderObj.ccExpires, "ccExpires");
			newOrder.setValue(orderObj.shippingCost, "shippingCost");
			newOrder.setValue(orderObj.shippingMethod, "shippingMethod");
			newOrder.setValue(orderObj.orderFinishedDate, "orderFinishedDate");
			newOrder.setValue(orderObj.comments, "comments");
			newOrder.setValue(orderObj.currency, "currency");
			newOrder.setValue(orderObj.currencyValue, "currencyValue");
			newOrder.setValue(orderObj.customerId, "customerId");

			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"OrderList_allOrders"
			);
			if (connection) {
				ConnectionHandler.insertEdgeAfter(connection, newOrder);
			}
		},
		updater: (newStore) => {
			const createOrderField = newStore.getRootField("createOrder");
			if (createOrderField) {
				const newOrder = createOrderField.getLinkedRecord("order");

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"OrderList_allOrders"
				);
				if (connection) {
					ConnectionHandler.insertEdgeAfter(connection, newOrder);
				}
			}
		},
		onCompleted: () => {
			cb();
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default CreateOrderMutation;
