import React from "react";
import {createFragmentContainer, graphql} from "react-relay";
import DeleteOrderProductMutation from "./DeleteOrderProductMutation";

interface IOrderProductProps {
	viewer: {
		id: string;
	};
	orderProduct: {
		id: string;
		order: {
			customerName: string;
			customer: {
				customerFirstname: string;
				customerLastname: string;
			};
		};
		product: {
			productDescription: {
				productName: string;
			};
		};
	};
}
const OrderProduct = (props: IOrderProductProps) => {
	const deleteOrderProductHandler = () => {
		DeleteOrderProductMutation(props.orderProduct.id, props.viewer.id);
	};

	return (
		<div className="grid-x shadow padding-1">
			<div className="cell text-right">
				<button
					type="button"
					className="button hollow"
					onClick={deleteOrderProductHandler}>
					Delete
				</button>
			</div>
			<div className="cell grid-x">
				id:
				<small className="cell margin-bottom-1">
					<b>{props.orderProduct.id}</b>
				</small>
			</div>
			<div className="cell grid-x">
				[Belong to] order-customerName:
				<small className="cell margin-bottom-1">
					<b>{props.orderProduct.order.customerName}</b>
				</small>
			</div>
			<div className="cell grid-x">
				[Belong to] order-customer-customerLastname, customerFirstname:
				<small className="cell margin-bottom-1">
					<b>
						{props.orderProduct.order.customer.customerLastname},{" "}
						{props.orderProduct.order.customer.customerFirstname}
					</b>
				</small>
			</div>
			<div className="cell grid-x">
				[Belong to] product-productDescription-productName:
				<small className="cell margin-bottom-1">
					<b>{props.orderProduct.product.productDescription.productName}</b>
				</small>
			</div>
		</div>
	);
};

export default createFragmentContainer(OrderProduct, {
	orderProduct: graphql`
		fragment OrderProduct_orderProduct on OrderProduct {
			id
			order {
				customerName
				customer {
					customerFirstname
					customerLastname
				}
			}
			product {
				productDescription {
					productName
				}
			}
		}
	`,
	viewer: graphql`
		fragment OrderProduct_viewer on Viewer {
			id
		}
	`
});
