import React, {Component} from "react";
import {createFragmentContainer, graphql} from "react-relay";
import DeleteProductMutation from "./DeleteProductMutation";

interface IProductProps {
	viewer: {
		id: string;
	};
	product: {
		id: string;
		productModel: string;
		productImage: string;
		productPrice: number;
		productQuantity: number;
		productDateAvailable: string;
		productWeight: number;
		productStatus: boolean;
		createdAt: string;
		updatedAt: string;
		productDescription: {
			id: string;
			productName: string;
			productOverview: string;
			productDescription: string;
		};
		shop: {
			shopName: string;
		};
		taxClass: {
			taxClassTitle: string;
		};
	};
}
const Product = (props: IProductProps) => {
	const deleteProductHandler = () => {
		DeleteProductMutation(
			props.product.productDescription.id,
			props.product.id,
			props.viewer.id
		);
	};

	return (
		<div className="grid-x shadow padding-1">
			<div className="cell text-right">
				<button
					type="button"
					className="button hollow"
					onClick={deleteProductHandler}>
					Delete
				</button>
			</div>
			<div className="cell grid-x">
				id:
				<small className="cell margin-bottom-1">
					<b>{props.product.id}</b>
				</small>
			</div>
			<div className="cell grid-x">
				productName:
				<small className="cell margin-bottom-1">
					<b>{props.product.productDescription.productName}</b>
				</small>
			</div>
			<div
				className="cell grid-x"
				title={props.product.productDescription.productOverview}>
				productOverview:
				<small className="cell margin-bottom-1 text-truncate">
					<b>{props.product.productDescription.productOverview}</b>
				</small>
			</div>
			<div className="cell grid-x">
				productDescription:
				<small className="cell margin-bottom-1 text-truncate">
					<b>{props.product.productDescription.productDescription}</b>
				</small>
			</div>
			<div className="cell grid-x">
				productModel:
				<small className="cell margin-bottom-1">
					<b>{props.product.productModel}</b>
				</small>
			</div>
			<div className="cell grid-x">
				productImage:
				<div className="cell margin-bottom-1 text-center">
					<img src={props.product.productImage} alt="Product image" />
				</div>
			</div>
			<div className="cell grid-x">
				productPrice:
				<small className="cell margin-bottom-1">
					<b>{props.product.productPrice}</b>
				</small>
			</div>
			<div className="cell grid-x">
				productQuantity:
				<small className="cell margin-bottom-1">
					<b>{props.product.productQuantity}</b>
				</small>
			</div>
			<div className="cell grid-x">
				productDateAvailable:
				<small className="cell margin-bottom-1">
					<b>{props.product.productDateAvailable}</b>
				</small>
			</div>
			<div className="cell grid-x">
				productWeight:
				<small className="cell margin-bottom-1">
					<b>{props.product.productWeight}</b>
				</small>
			</div>
			<div className="cell grid-x">
				productStatus:
				<small className="cell margin-bottom-1">
					<b>
						{props.product.productStatus === true
							? "Available"
							: "Not available"}
					</b>
				</small>
			</div>
			<div className="cell grid-x">
				createdAt:
				<small className="cell margin-bottom-1">
					<b>{props.product.createdAt}</b>
				</small>
			</div>
			<div className="cell grid-x">
				updatedAt:
				<small className="cell margin-bottom-1">
					<b>{props.product.updatedAt}</b>
				</small>
			</div>
			<div className="cell grid-x">
				[Belong to] shopName:
				<small className="cell margin-bottom-1">
					<b>{props.product.shop.shopName}</b>
				</small>
			</div>
			<div className="cell grid-x">
				[Belong to] taxClassTitle:
				<small className="cell margin-bottom-1">
					<b>{props.product.taxClass.taxClassTitle}</b>
				</small>
			</div>
		</div>
	);
};

export default createFragmentContainer(Product, {
	product: graphql`
		fragment Product_product on Product {
			id
			productModel
			productImage
			productPrice
			productQuantity
			productDateAvailable
			productWeight
			productStatus
			createdAt
			updatedAt
			productDescription {
				id
				productName
				productOverview
				productDescription
			}
			shop {
				shopName
			}
			taxClass {
				taxClassTitle
			}
		}
	`,
	viewer: graphql`
		fragment Product_viewer on Viewer {
			id
		}
	`
});
