import React, {Component} from "react";
import CategoryHeader from "./CategoryHeader/CategoryHeader";
import FilterOptionsWrapper from "./FilterOptionsWrapper/FilterOptionsWrapper";
import CategoryItems from "./CategoryItems/CategoryItems";
import {QueryRenderer, graphql} from "react-relay";
import environment from "../../schema/environment";

class CategoryPage extends Component {
	componentDidMount = () => {
		($("#CategoryPage") as any).foundation();
		window.scrollTo(0, 0);
	};

	render() {
		const CategoryPageAllProductsQuery = graphql`
			query CategoryPageAllProductsQuery {
				viewer {
					...CategoryItems_viewer
				}
			}
		`;

		return (
			<QueryRenderer
				environment={environment}
				query={CategoryPageAllProductsQuery}
				variables={{}}
				render={({error, props}) => {
					if (error) {
						return (
							<p>
								<b>Error: {error.message}</b>
							</p>
						);
					} else if (!props) {
						return (
							<p>
								<b>Loading...</b>
							</p>
						);
					} else {
						return (
							<div
								className="padding-horizontal-2 padding-vertical-1"
								id="CategoryPage">
								<CategoryHeader />
								<div className="grid-x grid-padding-x">
									<div className="cell medium-4 large-3">
										<FilterOptionsWrapper />
									</div>
									<div className="cell medium-auto">
										<CategoryItems viewer={props.viewer} />
									</div>
								</div>
							</div>
						);
					}
				}}
			/>
		);
	}
}

export default CategoryPage;
