import {commitMutation, graphql} from "react-relay";
import {ConnectionHandler} from "relay-runtime";
import uuid from "uuid";
import environment from "../../../../schema/environment";

const mutation = graphql`
	mutation CreateProductMutation($input: CreateProductInput!) {
		createProduct(input: $input) {
			product {
				id
				productModel
				productImage
				productPrice
				productQuantity
				productDateAvailable
				productWeight
				productStatus
				productDescription {
					productName
					productOverview
					productDescription
				}
				shop {
					shopName
				}
				taxClass {
					taxClassTitle
				}
			}
		}
	}
`;

function CreateProductMutation(
	productObj: {
		productName: string;
		productOverview: string;
		productDescription: string;
		productModel: string;
		productImage: string;
		productPrice: string;
		productQuantity: number;
		productDateAvailable: string;
		productWeight: number;
		productStatus: boolean;
		shopId: string;
		shopName: string;
		taxClassId: string;
		taxClassTitle: string;
		languageId: string;
	},
	viewerId: string,
	cb: () => void
) {
	const variables = {
		input: {
			productDescription: {
				productName: productObj.productName.trim(),
				productOverview: encodeURIComponent(productObj.productOverview.trim()),
				productDescription: encodeURIComponent(
					productObj.productDescription.trim()
				),
				languageId: productObj.languageId
			},
			productModel: productObj.productModel.trim(),
			productImage: productObj.productImage,
			productPrice: parseFloat(productObj.productPrice),
			productQuantity: productObj.productQuantity,
			productDateAvailable: productObj.productDateAvailable,
			productWeight: productObj.productWeight,
			productStatus: productObj.productStatus,
			shopId: productObj.shopId,
			taxClassId: productObj.taxClassId,
			clientMutationId: ""
		}
	};
	commitMutation(environment, {
		mutation,
		variables,
		optimisticUpdater: (oldStore) => {
			const id = uuid.v4();
			const newProduct = oldStore.create(id, "Product");
			newProduct.setValue(id, "id");
			newProduct.setValue(productObj.productName, "productName");
			newProduct.setValue(productObj.productOverview, "productOverview");
			newProduct.setValue(productObj.productDescription, "productDescription");
			newProduct.setValue(productObj.productModel, "productModel");
			newProduct.setValue(productObj.productImage, "productImage");
			newProduct.setValue(productObj.productPrice, "productPrice");
			newProduct.setValue(productObj.productQuantity, "productQuantity");
			newProduct.setValue(
				productObj.productDateAvailable,
				"productDateAvailable"
			);
			newProduct.setValue(productObj.productWeight, "productWeight");
			newProduct.setValue(productObj.productStatus, "productStatus");
			newProduct.setValue(productObj.shopId, "shopId");
			newProduct.setValue(productObj.shopName, "shopName");
			newProduct.setValue(productObj.taxClassId, "taxClassId");
			newProduct.setValue(productObj.taxClassTitle, "taxClassTitle");

			const viewerProxy = oldStore.get(viewerId);
			const connection = ConnectionHandler.getConnection(
				viewerProxy,
				"ProductList_allProducts"
			);
			if (connection) {
				ConnectionHandler.insertEdgeAfter(connection, newProduct);
			}
		},
		updater: (newStore) => {
			const getProductField = newStore.getRootField("createProduct");
			if (getProductField) {
				const newProduct = getProductField.getLinkedRecord("product");

				const viewerProxy = newStore.get(viewerId);
				const connection = ConnectionHandler.getConnection(
					viewerProxy,
					"ProductList_allProducts"
				);
				if (connection) {
					ConnectionHandler.insertEdgeAfter(connection, newProduct);
				}
			}
		},
		onCompleted: (variables, error) => {
			cb();
		},
		onError: (err) => {
			console.error(err);
		}
	});
}

export default CreateProductMutation;
