import React, {Component} from "react";
import {QueryRenderer, graphql} from "react-relay";
import environment from "../../../../schema/environment";
import OrderList from "./OrderList";

const OrderAppAllOrdersQuery = graphql`
	query OrderAppAllOrdersQuery {
		viewer {
			...OrderList_viewer
		}
	}
`;

const OrderApp = () => {
	return (
		<QueryRenderer
			environment={environment}
			query={OrderAppAllOrdersQuery}
			variables={{}}
			render={({error, props}) => {
				if (error) {
					return (
						<p>
							<b>Error: {error.message}</b>
						</p>
					);
				} else if (!props) {
					return (
						<p>
							<b>Loading...</b>
						</p>
					);
				} else {
					return <OrderList viewer={props.viewer} />;
				}
			}}
		/>
	);
};

export default OrderApp;
